import { useState, useContext, useEffect } from "react";
import { Post } from "../../utils/Request";
import { IsEmail } from "../../utils/Functions";

export default function LoginPage() {

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [loginType, setLoginType] = useState(null);

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const [emailMessage, setEmailMessage] = useState("");
    const [passwordMessage, setPasswordMessage] = useState("");


    // Obter e-mail e senha salvos do "Criar Conta":
    useEffect(() => {

        const savedEmail = localStorage["email"];
        const savedPassword = localStorage["password"];

        if (savedEmail) { setEmail(savedEmail) }
        if (savedPassword) { setPassword(savedPassword) }

        localStorage.removeItem("email");
        localStorage.removeItem("password");

    }, [])




    /**
     * Realizar o login do usuário usando suas credenciais.
     */
    async function LoginWithCredentials() {

        const validation = ValidateCredentials();
        if (!validation) { return; }
        setLoading(true);
        setLoginType(1);

        try {
            const result = await Post("/user/login/credentials", { email, password });
            localStorage['authorization'] = result.token;
            setTimeout(() => { window.location.href = "#/dashboard"; }, 1000);
        }
        catch (error) {
            console.error("FALHA NO LOGIN", error);
            setLoading(false);
            setError(error);
            document.getElementById("emailInput").focus();
        }

    }



    /**
     * Realizar o login do usuário usando sua conta do Google.
     */
    async function LoginWithGoogle(data) {

        console.log("login com google");

        // const profile = data.getBasicProfile();
        // const googleJWT = data.getAuthResponse().id_token;
        // const id = profile.getId();
        // const fullName = profile.getName();
        // const imageURL = profile.getImageUrl();
        // const email = profile.getEmail();

        const jwt = data.getAuthResponse().id_token;
        setLoading(true);
        setLoginType(2);

        try {
            const result = await Post("/user/login/google", jwt);
            localStorage['authorization'] = result.token;
            setTimeout(() => { window.location.href = "#/dashboard"; }, 1000);
        }
        catch (error) {
            console.error("FALHA NO LOGIN COM GOOGLE", error);
            setLoading(false);
            setError(error);
        }

    }

    // Declarar função externa na window, para a api do Google encontra-lá.
    window["onSignIn"] = LoginWithGoogle;

    function ValidateCredentials() {

        if (!email) {
            document.getElementById("emailInput").focus();
            setEmailMessage("Informe seu endereço de e-mail.");
            return false;
        }

        if (!IsEmail(email)) {
            document.getElementById("emailInput").focus();
            setEmailMessage("E-mail inválido.");
            return false;
        }

        if (!password) {
            document.getElementById("passwordInput").focus();
            setPasswordMessage("Digite sua senha.");
            return false;
        }

        return true;

    }

    function signOut() {
        try {
            var auth2 = window.gapi.auth2.getAuthInstance();
            auth2.signOut().then(function () { });
        } catch (error) {

        }
    }

    signOut();

    return (
        <>

            {/* Header */}
            <div className="appHeader no-border transparent position-absolute">
                <div className="pageTitle"></div>
                <div className="right">
                    {
                        !loading &&
                        <a href="/criar-conta" className="headerButton">
                            Criar Conta
                        </a>
                    }

                </div>
            </div>

            <div className="section mt-3 text-center">
                <h1 className="text-primary">XPEG Count</h1>
                <h4 className="text-secondary">Login</h4>
            </div>

            <div className="section mb-5 p-2">

                <div className="card">
                    <div className="card-body pb-1">

                        {/* Email */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (email ? "not-empty" : "")}>
                                <label className="label" htmlFor="emailInput">E-mail</label>
                                <input
                                    type="email"
                                    className="form-control"
                                    id="emailInput"
                                    placeholder="Informe seu e-mail"
                                    maxLength={80}
                                    disabled={loading}
                                    value={email}
                                    onKeyDown={() => setEmailMessage("")}
                                    onChange={(e) => setEmail(e.target.value)}
                                />
                                <i className="clear-input" onClick={() => setEmail("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info text-danger">{emailMessage}</div>
                        </div>

                        {/* Senha */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (password ? "not-empty" : "")}>
                                <label className="label" htmlFor="passwordInput">Senha</label>
                                <input
                                    type="password"
                                    className="form-control"
                                    id="passwordInput"
                                    placeholder="Digite sua senha"
                                    disabled={loading}
                                    value={password}
                                    onKeyDown={() => setPasswordMessage("")}
                                    onChange={(e) => setPassword(e.target.value)}
                                />
                                <i className="clear-input" onClick={() => setPassword("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info text-danger">{passwordMessage}</div>
                        </div>

                    </div>
                </div>

                {/* Caixa de Erro */}
                {
                    error &&
                    <div className="card text-white bg-danger my-5">
                        <div className="card-body">
                            <h5 className="card-title">{error.title || "Falha no Login"}</h5>
                            <ul className="card-text" >
                                {error.messages.map(message => <li>{message}</li>)}
                            </ul>
                        </div>
                    </div>
                }

                <div className="form-button-group transparent">
                    <button
                        type="button"
                        className="btn btn-primary btn-block btn-lg m-1"
                        disabled={loading}
                        onClick={() => LoginWithCredentials()}
                    >
                        {
                            loading && loginType == 1
                                ?
                                <>
                                    <i className="fas fa-spinner-third fa-spin icon me-2"></i>
                                    <span>Entrando...</span>
                                </>
                                :
                                <>
                                    <i className="fas fa-sign-in-alt icon me-1"></i>
                                    <span>Entrar</span>
                                </>
                        }
                    </button>

                    <button
                        type="button"
                        className="btn btn-danger btn-block btn-lg m-1"
                        disabled={loading}
                        onClick={() => {
                            document.getElementById('googleLoginButton').querySelector('*').click();
                        }}
                    >
                        {
                            loading && loginType == 2
                                ?
                                <>
                                    <i className="fas fa-spinner-third fa-spin icon me-2"></i>
                                    <span>Autenticando...</span>
                                </>
                                :
                                <>
                                    <i className="fab fa-google icon me-1"></i>
                                    <span>Google</span>
                                </>
                        }
                    </button>
                </div>

            </div>

        </>
    )
}