import { DateInWord, DecimalToMoney, Months } from "../../utils/Functions";

export default function Parcel(props) {

    const statusText = ['', "Pendente", "Aguardando Pagamento", "Atrasado", "Pago", "Parcialmente Pago"];
    const statusColor = ['', "bg-secondary", "bg-warning", "bg-danger", "bg-success", "bg-success"];

    const {  id, number, description, status, value, expirationDate, insertedDate } = props;

    const date = new Date(expirationDate);
    const month = date.getMonth() + 1;
    const year = date.getFullYear();

    return (
        <>
            <li>
                <a href={"#/parcela/" + id} className="item">
                    <div className="in">
                        <div>
                            <h3 className={"badge mb-1 " + statusColor[status]} style={{ borderRadius: 0, marginLeft: "-5px" }}>
                                {statusText[status]}
                            </h3>
                            <header>Parcela {number}</header>
                            {Months[month]} de {year}
                            {/* <footer>{description}</footer> */}

                        </div>
                        <span>R$ {DecimalToMoney(value)}</span>
                    </div>
                </a>
            </li>
        </>
    );
}