import { useEffect, useState } from "react"
import PageHeader from "../../components/App/PageHeader";
import { PRIMARY_COLOR } from "../../utils/App";

export default function MenuPage() {

    const [darkMode, setDarkMode] = useState(localStorage["darkmode"] == 'true');

    useEffect(() => {
        localStorage["darkmode"] = darkMode;
        document.querySelector('body').setAttribute('class', darkMode ? "dark-mode" : "");
    }, [darkMode])


    return (
        <>

            <PageHeader
                title="XPEG Count"
                color={PRIMARY_COLOR}
                showActionButton={false}
                showBackButton={false}
            />



            <div className="listview-title mt-1">Aplicativo</div>

            <ul className="listview image-listview text inset no-line">
                <li>
                    <div className="item">
                        <div className="in">
                            <div>
                                <i className="far fa-adjust icon me-1"></i>
                                <span>Modo Escuro</span>
                            </div>
                            <div className="form-check form-switch  ms-2">
                                <input
                                    className="form-check-input dark-mode-switch"
                                    type="checkbox"
                                    id="darkmodeSwitch"
                                    checked={darkMode}
                                    onChange={({ target }) => setDarkMode(target.checked)}
                                />
                                <label className="form-check-label" htmlFor="darkmodeSwitch"></label>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>

            <div className="listview-title mt-1">Gerenciar</div>

            <ul className="listview image-listview text inset">


                <li>
                    <a href={"#/cartoes-de-credito"} className="item">
                        <div className="in">
                            <div>
                                <i className="far fa-credit-card icon me-1"></i>
                                <span>Cartões de Crédito</span>
                            </div>
                        </div>
                    </a>
                </li>

                <li>
                    <a href={"#/categorias"} className="item">
                        <div className="in">
                            <div>
                                <i className="far fa-tags icon me-1"></i>

                                <span>Categorias</span>
                            </div>
                        </div>
                    </a>
                </li>

                <li>
                    <a href={"#/membros"} className="item">
                        <div className="in">
                            <div>
                                <i className="far fa-user-friends icon me-1"></i>

                                <span>Membros</span>
                            </div>
                        </div>
                    </a>
                </li>

            </ul>

            <div className="listview-title mt-1">Conta</div>
            <ul className="listview image-listview text inset">
                <li>
                    <a href={undefined} className="item">
                        <div className="in">

                            <div>
                                <i className="far fa-user-edit icon me-1"></i>

                                <span>Alterar Nome</span>
                            </div>
                        </div>
                    </a>
                </li>
                <li>
                    <a href={undefined} className="item">
                        <div className="in">
                            <div>
                                <i className="far fa-at icon me-1"></i>
                                <span>Alterar E-mail</span>
                            </div>
                        </div>
                    </a>
                </li>
            </ul>

            <div className="listview-title mt-1">Ajuda</div>
            <ul className="listview image-listview text inset">
                <li>
                    <a href={undefined} className="item" onClick={() => {

                        window.$("#ios-add-to-home-screen").modal("show");

                    }}>
                        <div className="in">
                            <div>
                                <i className="far fa-question-square icon me-1"></i>
                                <span>Instalar</span>
                            </div>
                        </div>
                    </a>
                </li>
            </ul>

            <div className="listview-title mt-1">Segurança</div>
            <ul className="listview image-listview text inset">
                <li>
                    <a href={undefined} className="item">
                        <div className="in">
                            <div>
                                <i className="far fa-key icon me-1"></i>
                                <span>Alterar Senha</span>
                            </div>
                        </div>
                    </a>
                </li>
                <li>
                    <a href="#/login" className="item">
                        <div className="in text-danger">
                            <div>
                                <i className="far fa-sign-out icon me-1"></i>
                                <span>Sair</span>
                            </div>
                        </div>
                    </a>
                </li>
            </ul>

        </>
    )

}