
export default function Category({ id, active, name, color, icon, sub = false }) {
    return (
        <li className={sub && "ps-5"} key={id} style={{ opacity: !active && ".2" }}>
            <a className={`item category-item ${sub && "category-item-sub"}`} href={`#/categoria/${id}`} category-id={id}>
                <div className="icon-box text-white" style={{ backgroundColor: color }}>
                    <i className={icon} />
                </div>
                <div className="in">
                    <div>{name}</div>
                </div>
            </a>
        </li>
    )
}