import { useState } from "react";
import PageHeader from "../../components/App/PageHeader";
import PageSection from "../../components/App/PageSection";

export default function CreateCreditCardPage() {

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const [name, setName] = useState("");
    const [flag, setFlag] = useState("");

    const [creditLimit, setCreditLimit] = useState("");
    const [annuityValue, setAnnuityValue] = useState("");
    const [invoiceCloseDay, setInvoiceCloseDay] = useState("");
    const [invoiceExirationDay, setInvoiceExirationDay] = useState("");
    const [main, setMain] = useState("");

    const [nameMessage, setNameMessage] = useState("");

    async function AddCreditCard() {

    }

    return (
        <>
            <PageHeader
                title="Adicionar Cartão"
                showBackButton={true}
                showActionButton={false}
            />


            <PageSection>

                <div className="card">
                    <div className="card-body">

                        {/* Nome */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (name ? "not-empty" : "")}>
                                <label className="label" htmlFor="nameInput">Nome</label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="nameInput"
                                    maxLength={50}
                                    placeholder="Ex. NuBank"
                                    disabled={loading}
                                    value={name}
                                    onKeyDown={() => setNameMessage("")}
                                    onChange={({ target }) => setName(target.value)}
                                />
                                <i className="clear-input" onClick={() => setName("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className={`input-info ${nameMessage && "text-danger"}`}>
                                {nameMessage}
                            </div>
                        </div>

                    </div>
                </div>


                <div className="form-button-group transparent">

                    <button
                        className="btn btn-block btn-lg btn-primary"
                        disabled={loading}
                        onClick={() => AddCreditCard()}
                    >
                        {
                            loading ?
                                <>
                                    <i className="fas fa-spinner-third fa-spin icon me-2"></i>
                                    <span>Adicionando...</span>
                                </>
                                :
                                <>
                                    <span>Adicionar Cartão</span>
                                </>
                        }

                    </button>


                </div>

            </PageSection>
        </>
    )

}