import { useEffect, useState } from "react";

export default function ChangeNameDialog({ title, oldValue, confirmCallback = () => { }, cancelCallback = () => { } }) {

    const [value, setValue] = useState();

    useEffect(() => {
        setValue(oldValue);
    }, [oldValue])


    return (
        <div className="modal fade dialogbox" id="ChangeNameDialog" data-bs-backdrop="static" tabIndex="-1" aria-hidden="true">
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title">{title}</h5>
                    </div>

                    <div className="modal-body text-start mb-2">

                        <div className="form-group basic">
                            <div className={"input-wrapper " + (value ? "not-empty" : "")}>
                                <label className="label" htmlFor="newValueInput">Nome:</label>

                                <input
                                    type="text"
                                    className="form-control"
                                    id="newValueInput"
                                    value={value}
                                    onChange={({ target }) => setValue(target.value)}
                                />
                                <i
                                    className="clear-input"
                                    onClick={() => { setValue(""); }}
                                >
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                        </div>

                    </div>
                    <div className="modal-footer">
                        <div className="btn-inline">
                            <button type="button" className="btn btn-text-secondary" data-bs-dismiss="modal">CANCELAR</button>
                            <button
                                type="button"
                                className="btn btn-text-primary"
                                data-bs-dismiss="modal"
                                onClick={() => confirmCallback({ value })}
                            >
                                CONFIRMAR
                            </button>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    )
}