import { useState } from "react";
import { IsEmail } from "../../utils/Functions";
import { Post } from "../../utils/Request";
import ReCAPTCHA from "react-google-recaptcha";

export default function RegisterPage() {

    const siteKey = "6LcN_4klAAAAADo2wB2cShFBXqnUt_Z0dz3Njl35";
    const [recaptchaToken, setRecaptchaToken] = useState('');

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const [name, setName] = useState("");
    const [birthdate, setBirthdate] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const [nameMessage, setNameMessage] = useState("");
    const [birthdateMessage, setBirthdateMessage] = useState("");
    const [emailMessage, setEmailMessage] = useState("");
    const [passwordMessage, setPasswordMessage] = useState("");

    async function CreateAccount() {

        const validation = ValidateInputs();
        if (!validation) { return; }

        setError(null);
        setLoading(true);

        try {
            const result = await Post(`/user/register`, { name, birthdate, email, password, recaptchaToken });
            localStorage["email"] = email;
            localStorage["password"] = password;
            setTimeout(() => { window.location.href = "/login"; }, 1000);
        }
        catch (error) {
            console.error("FALHA AO CRIAR USUÁRIO", error);
            setLoading(false);
            setError(error);
        }

    }


    function ValidateInputs() {

        if (!name) {
            document.getElementById("nameInput").focus();
            setNameMessage("Digite seu nome completo.");
            return false;
        }

        if (name.split(" ").length == 1) {
            document.getElementById("nameInput").focus();
            setNameMessage("Digite seu nome completo.");
            return false;
        }

        console.log("birthdate", birthdate);
        if (!birthdate) {
            document.getElementById("dateInput").focus();
            setBirthdateMessage("Selecione sua data de nascimento.");
            return false;
        }

        if (!email) {
            document.getElementById("emailInput").focus();
            setEmailMessage("Digite seu endereço de e-mail.");
            return false;
        }

        if (!IsEmail(email)) {
            document.getElementById("emailInput").focus();
            setEmailMessage("E-mail inválido.");
            return false;
        }

        if (!password) {
            document.getElementById("passwordInput").focus();
            setPasswordMessage("Digite sua senha.");
            return false;
        }

        if (!recaptchaToken) {
            alert("Valida o reCaptaccha antes de prosseguir.")
            return false;
        }

        return true;
    }

    return (

        <>
            {/* Header */}
            <div className="appHeader no-border transparent position-absolute">

                <div className="left">
                    {
                        !loading &&
                        <a href={undefined} className="headerButton" onClick={() => window.history.back()}>
                            <i className="fal fa-chevron-left icon"></i>
                        </a>
                    }

                </div>

                <div className="pageTitle"></div>

            </div>


            <div className="section mt-3 text-center">
                <h1 className="text-primary">Criar Conta</h1>
                <h4 className="text-secondary">Preencha seus dados para criar uma nova conta.</h4>
            </div>

            <div className="section mb-5 p-2">

                <div className="card">
                    <div className="card-body pb-1">

                        {/* Nome */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (name ? "not-empty" : "")}>
                                <label className="label" htmlFor="nameInput">Nome Completo</label>
                                <input
                                    type="name"
                                    className="form-control"
                                    id="nameInput"
                                    placeholder="Digite seu nome completo"
                                    maxLength={50}
                                    disabled={loading}
                                    value={name}
                                    onKeyDown={() => setNameMessage("")}
                                    onChange={(e) => setName(e.target.value)}
                                />
                                <i className="clear-input" onClick={() => setName("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info text-danger">{nameMessage}</div>
                        </div>

                        {/* Data de Nascimento */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (birthdate ? "not-empty" : "")}>
                                <label className="label" htmlFor="dateInput">Data de Nascimento</label>
                                <input
                                    type="date"
                                    className="form-control"
                                    id="dateInput"
                                    placeholder="Selecione sua data de nascimento"
                                    disabled={loading}
                                    value={birthdate}
                                    onChange={(e) => {
                                        setBirthdate(e.target.value);
                                        setBirthdateMessage("");
                                    }}
                                />
                                <i className="clear-input" onClick={() => setBirthdate("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info text-danger">{birthdateMessage}</div>
                        </div>

                        {/* Email */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (email ? "not-empty" : "")}>
                                <label className="label" htmlFor="emailInput">E-mail</label>
                                <input
                                    type="email"
                                    className="form-control"
                                    id="emailInput"
                                    placeholder="Digite seu endereço de e-mail"
                                    maxLength={80}
                                    disabled={loading}
                                    value={email}
                                    onKeyDown={() => setEmailMessage("")}
                                    onChange={(e) => setEmail(e.target.value)}
                                />
                                <i className="clear-input" onClick={() => setEmail("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info text-danger">{emailMessage}</div>
                        </div>

                        {/* Senha */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (password ? "not-empty" : "")}>
                                <label className="label" htmlFor="passwordInput">Senha</label>
                                <input
                                    type="password"
                                    className="form-control"
                                    id="passwordInput"
                                    placeholder="Digite sua senha"
                                    autoComplete={"off"}
                                    disabled={loading}
                                    value={password}
                                    onKeyDown={() => setPasswordMessage("")}
                                    onChange={(e) => setPassword(e.target.value)}
                                />
                                <i className="clear-input" onClick={() => setPassword("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info text-danger">{passwordMessage}</div>
                        </div>

                    </div>
                </div>

                <div className="d-flex justify-content-center mt-2">
                    <ReCAPTCHA
                        sitekey={siteKey}
                        onChange={(value) => setRecaptchaToken(value)}
                    />
                </div>


                {/* Caixa de Erro */}
                {
                    error &&
                    <div className="card text-white bg-danger my-5">
                        <div className="card-body">
                            <h5 className="card-title">{error.title || "Falha no Login"}</h5>
                            <ul className="card-text" >
                                {error.messages.map(message => <li>{message}</li>)}
                            </ul>
                        </div>
                    </div>
                }

            </div>

            <div className="form-button-group transparent">
                <button
                    type="button"
                    className="btn btn-primary btn-block btn-lg m-1"
                    disabled={loading || !recaptchaToken}
                    onClick={() => CreateAccount()}
                >
                    {
                        loading
                            ?
                            <>
                                <i className="fas fa-spinner-third fa-spin icon me-2"></i>
                                <span>Criando sua conta...</span>
                            </>
                            :
                            <>
                                <i className="far fa-check icon me-1"></i>
                                <span>Criar Conta</span>
                            </>
                    }
                </button>
            </div>

        </>

    )

}