export default function ConfirmDialog({ id = "ConfirmDialog", title, message, confirmButton = "btn btn-text-primary", confirmCallback = () => { }, cancelCallback = () => { } }) {

    return (
        <>
            <div className="modal fade dialogbox" id={id} data-bs-backdrop="static" tabIndex="-1" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">{title}</h5>
                        </div>
                        <div className="modal-body">
                            {message}
                        </div>
                        <div className="modal-footer">
                            <div className="btn-inline">
                                <a
                                    href={undefined}
                                    className="btn btn-text-secondary"
                                    data-bs-dismiss="modal"
                                    onClick={() => cancelCallback()}>
                                    CANCELAR
                                </a>

                                <a
                                    href={undefined}
                                    className={confirmButton}
                                    data-bs-dismiss="modal"
                                    onClick={() => confirmCallback()}>
                                    CONFIRMAR
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );

}