import { useState } from "react";
import Expense from "../../components/Expenses/Expense";
import { useEffect } from "react";
import { Get } from "../../utils/Request";
import PageHeader from "../../components/App/PageHeader";
import { DANGER_COLOR, SetThemeColor } from "../../utils/App";

export default function ManagerExpensesPage() {

    const [categories, setCategories] = useState([]);
    const [expenses, setExpenses] = useState([]);
    const [showFilter, setShowFilter] = useState(false);

    const [titleFilter, setTitleFilter] = useState("");
    const [descriptionFilter, setDescriptionFilter] = useState("");
    const [statusFilter, setStatusFilter] = useState("");
    const [categoryFilter, setCategoryFilter] = useState("");

    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [categoryID, setCategoryID] = useState("");

    useEffect(() => {

        SetThemeColor(DANGER_COLOR);

        (async () => {
            await GetCategories();
            GetExpenses();
        })()

    }, [])

    function log() {
        console.log("titleFilter", titleFilter);
        console.log("descriptionFilter", descriptionFilter);
        console.log("statusFilter", statusFilter);
        console.log("categoryFilter", categoryFilter);
    }

    /**
     * Buscar todas as categorias do usuário.
     */
    const GetCategories = async function () {
        try {
            const result = await Get('/categories/all?withSubs=true');
            setCategories(result);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR CATEGORIAS", error);
        }
    }

    /**
     * Buscar despesas do usuário.
     */
    const GetExpenses = async function () {
        try {
            const result = await Get(`/expenses/list?title=${title}&description=${description}&categoryid=${categoryID}`);
            setExpenses(result);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR DESPESAS", error);
        }
    }


    return (
        <>

            <PageHeader
                title={"Despesas"}
                color={DANGER_COLOR}
                showBackButton={false}
                showActionButton={true}
                actionButtonIcon="fa-plus"
                actionButtonCallback={() => {
                    window.location.href = "#/criar-despesa";
                }}
            />


            <div className="section mt-2 mb-2">

                {
                    showFilter && <>
                        <div className="card mb-2">
                            <div className="card-header">
                                <h5 className=" mb-0 pb-0">
                                    <i className="fas fa-filter icon me-1"></i>
                                    Filtro
                                </h5>
                            </div>

                            <div className="card-body">
                                <form>

                                    <div className="form-group basic">
                                        <div className="input-wrapper">
                                            <label className="label" htmlFor="expenseTitleInput">Título</label>
                                            <input
                                                type="text"
                                                className="form-control"
                                                id="expenseTitleInput"
                                                placeholder="Filtrar por título."
                                                onChange={(e) => setTitleFilter(e.target.value)}
                                            />
                                        </div>
                                    </div>

                                    <div className="form-group basic">
                                        <div className="input-wrapper">
                                            <label className="label" htmlFor="expenseDescriptionInput">Descrição</label>
                                            <input
                                                type="text"
                                                className="form-control"
                                                id="expenseDescriptionInput"
                                                placeholder="Filtrar por descrição"
                                                onChange={(e) => setDescriptionFilter(e.target.value)}
                                            />
                                        </div>
                                    </div>

                                    <div className="form-group basic">
                                        <div className="input-wrapper">
                                            <label className="label" htmlFor="expenseStatusSelect">Status</label>
                                            <select
                                                className="form-control custom-select"
                                                id="expenseStatusSelect"
                                                onChange={(e) => setStatusFilter(e.target.value == "-1" ? null : e.target.value)}
                                            >
                                                <option value="-1" defaultValue>Todas</option>
                                                <option value="1">Pagamento em Dia</option>
                                                <option value="2">Pendente</option>
                                                <option value="3">Atrasada</option>
                                                <option value="4">Finalizada</option>
                                            </select>
                                        </div>
                                    </div>

                                    <div className="form-group basic">
                                        <div className="input-wrapper">
                                            <label className="label" htmlFor="expenseCategorySelect">Categoria</label>
                                            <select
                                                className="form-control custom-select"
                                                id="expenseCategorySelect"
                                                onChange={(e) => setCategoryFilter(e.target.value == "-1" ? null : e.target.value)}
                                            >
                                                <option value="-1" defaultValue>Todas</option>
                                                {
                                                    categories.map(({ id, name }) => <option value={id}>{name}</option>)
                                                }
                                            </select>
                                        </div>
                                    </div>

                                </form>
                            </div>

                            <div className="card-footer d-flex justify-content-end">
                                <button className="btn btn-outline-white">Limpar</button>
                                <button className="btn btn-primary" onClick={() => GetExpenses()}>Pesquisar</button>
                            </div>

                        </div>
                    </>
                }


                <div className="transactions">
                    {
                        categories.map(({ id, name, icon, color, subCategories = [] }) => {

                            const subCategoriesIDs = subCategories.map(x => x.id);
                            subCategoriesIDs.push(id);

                            const categoryExpenses = expenses.filter(expense => subCategoriesIDs.includes(expense.categoryID));
                            if (categoryExpenses.length == 0) { return (<></>) }

                            return (
                                <>
                                    <div className="section-title pt-2 pb-2" style={{ color }}>
                                        <i className={"icon me-1 " + icon}></i>
                                        {name}
                                    </div>

                                    {
                                        categoryExpenses.map((expense) => <Expense {...expense} />)
                                    }
                                </>
                            )
                        })
                    }
                </div>
            </div>

        </>
    )
}