export default function SelectIconModal(
    {
        oldIcon,
        confirmCallBack = () => { }
    }
) {

    const [value, setValue] = useState(oldIcon);

    useEffect(() => {
        setValue(oldIcon);
    }, [oldIcon])

    return (
        <div class="modal fade action-sheet" id="SelectIconModal" tabindex="-1" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    {/* <div class="modal-header">
                        <h5 class="modal-title">Selecionar Cor</h5>
                    </div> */}
                    <div class="modal-body">
                        <div class="action-sheet-content" style={{ maxHeight: "550px" }}>
                            <form className="d-flex  justify-content-start align-items-center flex-wrap">

                                <h5 className="d-block w-100">ANIMAIS</h5>
                                {
                                    ANIMALS_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">COMIDA</h5>
                                {
                                    FOOD_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">FERRAMENTAS</h5>
                                {
                                    TOOLS_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">FINANÇAS</h5>
                                {
                                    MONEY_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">LOCAIS</h5>
                                {
                                    PLACES_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">PESSOAS</h5>
                                {
                                    USERS_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">TECNOLOGIA</h5>
                                {
                                    TECH_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">VEÍCULOS</h5>
                                {
                                    VEHICLE_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }

                                <h5 className="d-block w-100 mt-2">VESTUÁRIO</h5>
                                {
                                    CLOTHING_ICONS.map((x, index) => <Icon key={index} icon={x} selectedIcon={value} setIcon={confirmCallBack} />)
                                }


                                {/* <div class="form-group basic mt-2">
                                    <button
                                        type="button"
                                        class="btn btn-primary btn-block btn-lg"
                                        data-bs-dismiss="modal"
                                        onClick={() => { confirmCallBack({ value }) }}>
                                        Concluído
                                    </button>
                                </div> */}
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );

}


export function Icon({ icon, selectedIcon, setIcon, modal = true }) {
    return (
        <div
            className={"icon-item " + (icon == selectedIcon && "selected")}
            data-bs-dismiss={modal ? "modal" : "none"}
            onClick={() => setIcon({ value: icon })}
        >
            <i className={icon} />
        </div>
    );
}