import { useEffect, useState } from "react"
import { Get } from "../utils/Request";

export default function ChangeCategoryDialog({ title, confirmCallback }) {

    const [categories, setCategories] = useState([]);
    const [category, setCategory] = useState(null);

    useEffect(() => {
        GetCategories();
    }, [])

    async function GetCategories() {
        try {
            const result = await Get("/categories/all");
            setCategories(result);
        }
        catch (error) {

        }
    }


    return (
        <>
            <div className="modal fade dialogbox" id="ChangeCategoryDialog" data-bs-backdrop="static" tabIndex="-1" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">{title}</h5>
                        </div>

                        <div className="modal-body text-start mb-2">

                            <div className="form-group basic">
                                <div className={"input-wrapper"}>

                                    <div className="dropdown dropdown-select">
                                        <button
                                            className="btn btn-block btn-outline-secondary dropdown-toggle justify-content-start"
                                            id="selectCategoryButton"
                                            type="button"
                                            data-bs-toggle="dropdown"
                                            aria-expanded="false"
                                            style={{ backgroundColor: category && category.color, color: category && "white" }}
                                        >
                                            {
                                                category ?
                                                    <>
                                                        <i className={`${category.icon}  me-1`} />
                                                        <span>{category.name}</span>
                                                    </>
                                                    :
                                                    <>
                                                        <i className="fas fa-tag me-1"></i>
                                                        <span>Selecionar Categoria</span>
                                                    </>

                                            }
                                        </button>
                                        <div className="dropdown-menu dropdown-menu-end" style={{ minWidth: "100%", maxHeight: "280px", overflowY: "auto" }}>

                                            {
                                                categories.map((item, x) => {

                                                    const subs = item.subCategories || [];

                                                    return (
                                                        <>

                                                            <a
                                                                key={x}
                                                                href={undefined}
                                                                className={`dropdown-item`}
                                                                onClick={() => {
                                                                    document.getElementById("selectCategoryButton").classList.remove("btn-outline-secondary")
                                                                    setCategory(item);
                                                                }}
                                                            >
                                                                <i className={`${item.icon} icon me-1`} style={{ color: item.color }} />
                                                                {item.name}
                                                            </a>

                                                            {
                                                                subs.map((sub, y) => {
                                                                    return (
                                                                        <a
                                                                            key={y}
                                                                            href={undefined}
                                                                            className={`dropdown-item`}
                                                                            style={{ paddingLeft: "36px" }}
                                                                            onClick={() => {
                                                                                document.getElementById("selectCategoryButton").classList.remove("btn-outline-secondary")
                                                                                setCategory({
                                                                                    id: item.id,
                                                                                    subCategoryID: sub.id,
                                                                                    name: sub.name,
                                                                                    icon: item.icon,
                                                                                    color: sub.color
                                                                                });
                                                                            }}
                                                                        >
                                                                            <i className={`fas fa-circle icon me-1`} style={{ color: item.color, fontSize: "10px" }} />
                                                                            {sub.name}
                                                                        </a>
                                                                    )
                                                                })
                                                            }

                                                        </>
                                                    );

                                                }

                                                )
                                            }

                                        </div>
                                    </div>

                                </div>
                            </div>

                        </div>
                        <div className="modal-footer">
                            <div className="btn-inline">
                                <button type="button" className="btn btn-text-secondary" data-bs-dismiss="modal">CANCELAR</button>
                                <button
                                    type="button"
                                    className="btn btn-text-primary"
                                    data-bs-dismiss="modal"
                                    onClick={() => confirmCallback({ category })}
                                >
                                    CONFIRMAR
                                </button>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </>
    )
}