import { useEffect } from "react";
import { useState } from "react";
import { NavLink } from "react-router-dom";

export default function AppBottomMenu() {

    const [actualPage, setActualPage] = useState("");
    const pagesWithMenu = [
        "/dashboard",
        "/despesas",
        "/carteiras",
        "/menu",
    ];

    const ignoreMenuInPages = [
        "/login",
        "/criar-despesa"
    ]


    useEffect(() => {

        const hash = window.location.hash.replace("#", "");
        setActualPage(hash);

      
    }, []);

    window.onhashchange = function () {
        const hash = window.location.hash.replace("#", "");
        setActualPage(hash);



    }


    return (
        <>
            <div className={`appBottomMenu ${ignoreMenuInPages.includes(actualPage) && "hidden"} `}>

                <a href="#/dashboard" className={("item ") + ((actualPage == "/dashboard" || actualPage == "/") ? "active" : "")} title="Dashboard">
                    <div className="col">
                        <i className="fal fa-home-alt icon"></i>
                        <strong>Início</strong>
                    </div>
                </a>
                <a href="#/despesas" className={("item ") + (actualPage == "/despesas" ? "active" : "")} title="Despesas">
                    <div className="col">
                        <i className="fal fa-sack-dollar icon"></i>
                        <strong>Despesas</strong>
                    </div>
                </a>

                <a href="#/carteiras" className={("item ") + (actualPage == "/carteiras" ? "active" : "")} title="Carteiras">
                    <div className="col">
                        <i className="fal fa-wallet icon"></i>
                        <strong>Carteiras</strong>
                    </div>
                </a>


                <a href="#/menu" className={("item ") + (actualPage == "/menu" ? "active" : "")} title="Perfil">
                    <div className="col">
                        <i className="fal fa-bars icon"></i>
                        <strong>Menu</strong>
                    </div>
                </a>

            </div>
        </>
    )
}