import { useEffect } from "react";
import { useState } from "react";

const COLORS = [
    "#4496C8",
    "#8D3BC4",
    "#71962C",
    "#EF9034",
    "#BB271B",
    "#58AEDD",
    "#BC8DDD",
    "#97C15D",
    "#F6BF4A",
    "#EB544D",
    "#CFAEE5",
    "#CBE17C",
    "#F7DA8C",
    "#F19997",
    "#3B3B3B",
    "#686868",
    "#8F8F8F",
    "#BCBCBC",
    "#2715F3",
    "#5B9795",
    "#1E4D16",
    "#F6D957",
    "#C4C840",
    "#981D99",
    "#B07026",
    "#861A10",
    "#A4B5BF",
    "#43827A",
    "#FDEA60",
];

export default function SelectColorModal(
    {
        oldColor = COLORS[0],
        confirmCallBack = () => { }
    }
) {

    useEffect(() => {

        setValue(oldColor);

    }, [oldColor])

    const [value, setValue] = useState(oldColor);

    return (
        <div class="modal fade action-sheet" id="SelectColorModal" tabindex="-1" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    {/* <div class="modal-header">
                        <h5 class="modal-title">Selecionar Cor</h5>
                    </div> */}
                    <div class="modal-body">
                        <div class="action-sheet-content">
                            <form className="d-flex  justify-content-start align-items-center flex-wrap">


                                {
                                    COLORS.map((c, index) =>
                                        <Color
                                            color={c}
                                            selectedColor={value}
                                            setColor={({ value }) => setValue(value)}
                                        />)
                                }


                                <div class="form-group basic mt-2">
                                    <button
                                        type="button"
                                        class="btn btn-primary btn-block btn-lg"
                                        data-bs-dismiss="modal"
                                        onClick={() => { confirmCallBack({ value }) }}>
                                        Concluído
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );

}


export function Color({ color, selectedColor, setColor }) {
    return (
        <div
            className={"color-item " + (color == selectedColor && "selected")}
            style={{ backgroundColor: color }}
            onClick={() => { setColor({ value: color }) }}
        />
    );
}