import { NewGuid } from "../utils/Functions";

export default function FormInput(props) {

    const {
        id = NewGuid(),
        classComplement = "",
        type = "text",
        label,
        icon,
        maxLength = 100,
        placeholder = "",
        disabled = null,
        value = null,
        setValue = () => { },
        message = null,
        setMessage = () => { },
        displayLength = true
    } = props;


    return (
        <>
            <div className={`form-group basic ${classComplement}`}>
                <div className={`input-wrapper ${value && "not-empty"}`}>
                    <label className="label" htmlFor={id}>
                        <i className={`${icon} icon me-1`} />
                        {label}
                    </label>
                    <input
                        id={id}
                        className="form-control"
                        type={type}
                        maxLength={maxLength}
                        placeholder={placeholder}
                        disabled={disabled}
                        value={value}
                        onKeyDown={() => setMessage("")}
                        onChange={({ target }) => setValue(target.value)}
                    />
                    <i className="clear-input" onClick={() => setValue("")}>
                        <i className="fal fa-times icon"></i>
                    </i>
                </div>
                <div className="input-info">
                    <span className="text-danger">
                        {message}
                    </span>
                    <span className="float-end">
                        {displayLength && (maxLength - value.length)}
                    </span>
                </div>
            </div>
        </>
    )
}