import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Delete, Get, Patch, Post, Put } from "../../utils/Request";
import { DateInWord } from "../../utils/Functions";
import Skeleton from "react-loading-skeleton";
import { Dropdown, DropdownDivider, DropdownHeader, DropdownItem, DropdownMenu } from "../../components/Template/Dropdown";
import ChangeCategoryNameDialog from "../../modals/Categories/ChangeCategoryNameDialog";
import ChangeNameDialog from "../../dialogs/changeName";
import { OpenModal } from "../../utils/Modals";
import SuccessDialog from "../../dialogs/success";
import ConfirmDialog from "../../dialogs/confirm";
import FullLoader from "../../components/App/FullLoader";
import LineShimmer from "../../components/Shimmer/LineShimmer";
import BoxShimmer from "../../components/Shimmer/BoxShimmer";
import PageCard from "../../components/App/PageCard";
import SubCategory from "../../components/Categories/SubCategory";
import PageHeader from "../../components/App/PageHeader";
import SelectColorModal from "../../modals/SelectColor";
import SelectIcon from "../../modals/SelectIcon";
import SelectIconModal from "../../modals/SelectIcon";
import SelectCategoryModal from "../../modals/SelectCategory";
import Category from "../../components/Categories/Category";
import { SetThemeColor } from "../../utils/App";


export default function ViewCategoryPage() {

    const { id } = useParams();

    const [loadingFull, setLoadingFull] = useState(true);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [success, setSuccess] = useState(null);

    const [category, setCategory] = useState({});
    const [subCategories, setSubCategories] = useState([]);
    const { name, description, icon, color, type, active, isSubCategory, amountExpenses, insertedDate } = category;

    const DeleteCategoryConfirm = ConfirmDialog;
    const DisabledCategoryConfirm = ConfirmDialog;

    window.onhashchange = async () => {
        await GetCategory();
        await GetSubCategories();
        setLoading(false);
    }

    useEffect(() => {

        (async () => {

            await GetCategory();
            await GetSubCategories();
            setLoading(false);

        })()


    }, []);

    /**
     * Obter informações da categoria.
     */
    async function GetCategory() {
        setLoading(true);
        try {
            const result = await Get(`/categories/${id}`);
            SetThemeColor(result.color);
            setCategory(result);
            setLoading(false);
            setLoadingFull(false);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR CATEGORIA", error);
            setLoading(false);
        }
    }

    /**
     * Obter sub-categorias da categoria pai.
     */
    async function GetSubCategories() {
        try {
            const result = await Get(`/categories/${id}/sub`);
            setSubCategories(Array.isArray(result) ? result : []);
        }
        catch (error) {
            console.error("FALHA AO OBTER SUB-CATEGORIAS", error);
        }
    }

    /**
     * Transferir despesas para outra categoria.
     */
    async function TransferExpenses({ value }) {

        alert("categoria selecionada: " + value.name);
        return;

        setLoading(true);
        setError(null);

        try {
            await Post(`/categories/${id}/transfer`, { categoryID: value });
            ShowSuccessMessage("Transfêrencia Concluída", `Todas as despesas foram transferidas com êxito.`);
        }
        catch (error) {
            console.error("FALHA AO TRANSFERIR DESPESAS", error);
            setLoading(false);
            setError(error);
        }
    }


    /**
     * Alterar status da categoria.
     */
    const ChangeStatus = async function () {

        setLoading(true);
        setError(null);

        try {
            await Patch(`/categories/${id}/status`, { active: !active });
            setLoading(false);
            ShowSuccessMessage("Status Alterado", `A categoria foi ${active ? "desativada" : "ativada"} com êxito.`);
            GetCategory();
            GetSubCategories();
        }
        catch (error) {
            console.error("FALHA AO ATUALIZAR NOME DA CATEGORIA", error);
            setLoading(false);
            setError(error);
        }

    }


    /**
     * Alterar nome da categoria.
     */
    const ChangeName = async function ({ value }) {

        setLoading(true);
        setError(null);

        try {
            await Patch(`/categories/${id}/name`, { name: value });
            setLoading(false);
            ShowSuccessMessage("Nome Alterado", "O nome foi alterado com êxito.");
            GetCategory();
            GetSubCategories();
        }
        catch (error) {
            console.error("FALHA AO ATUALIZAR NOME DA CATEGORIA", error);
            setLoading(false);
            setError(error);
        }

    }


    /**
     * Alterar ícone da categoria.
     */
    const ChangeIcon = async function ({ value }) {

        if (value == icon) { return }

        setLoading(true);
        setError(null);

        try {
            await Patch(`/categories/${id}/icon`, { icon: value });
            setLoading(false);
            ShowSuccessMessage("Ícone Alterado", "O ícone foi alterado com êxito.");
            GetCategory();
            GetSubCategories();
        }
        catch (error) {
            console.error("FALHA AO ATUALIZAR ÍCONE DA CATEGORIA", error);
            setLoading(false);
            setError(error);
        }

    }

    /**
     * Alterar cor da categoria.
     */
    const ChangeColor = async function ({ value }) {

        if (value == color) { return }

        setLoading(true);
        setError(null);

        try {
            await Patch(`/categories/${id}/color`, { color: value });
            setLoading(false);
            ShowSuccessMessage("Cor Alterado", "A cor foi alterada com êxito.");
            GetCategory();
            GetSubCategories();
        }
        catch (error) {
            console.error("FALHA AO ATUALIZAR COR DE DESTAQUE DA CATEGORIA", error);
            setLoading(false);
            setError(error);
        }

    }

    /**
     * Excluir categoria.
     */
    const DeleteCategory = async function () {

        setLoading(true);
        setError(null);

        try {
            await Delete(`/categories/${id}`);
            setLoading(false);
            window.location.href = "#/categorias";
        }
        catch (error) {
            console.error("FALHA AO EXCLUIR CATEGORIA", error);
            setLoading(false);
            setError(error);
        }

    }

    function ShowSuccessMessage(title, message) {
        setSuccess({ title, message });
        OpenModal("#SuccessDialog");
    }

    return (
        <>

            {/* <SelectCategoryModal confirmCallBack={TransferExpenses} /> */}

            <SelectColorModal oldColor={color} confirmCallBack={ChangeColor} />

            <SelectIconModal oldIcon={icon} confirmCallBack={ChangeIcon} />

            {/* <FullLoader show={loadingFull} transparent={true} /> */}

            <ChangeNameDialog title={"Alterar Nome"} oldValue={name} confirmCallback={ChangeName} />

            <SuccessDialog {...success} />

            <DeleteCategoryConfirm
                id={"DeleteCategoryConfirm"}
                title={"Excluir Categoria"}
                message={"Tem certeza que deseja excluir essa categoria?"}
                confirmButton={"btn btn-text-danger"}
                confirmCallback={DeleteCategory}
            />


            <DisabledCategoryConfirm
                id={"DisabledCategoryConfirm"}
                title={active ? "Desativar Categoria" : "Ativar Categoria"}
                message={`Tem certeza que deseja ${active ? "desativar" : "ativar"} a categoria?`}
                confirmCallback={ChangeStatus}
            />

            <PageHeader title={"Detalhes"} color={color} showBackButton={true} showActionButton={false}>

                <Dropdown>

                    <a className="headerButton text-white" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                        <i className="fal fa-ellipsis-v icon"></i>
                    </a>

                    <DropdownMenu style={{ width: "300px" }}>

                        <DropdownHeader text={"Alterar"} />

                        <DropdownItem disabled={loading} onClick={() => { OpenModal("#ChangeNameDialog") }}>
                            <i className="far fa-quote-right icon"></i>
                            <span>Nome</span>
                        </DropdownItem>

                        <DropdownItem disabled={loading}
                            onClick={() => { OpenModal("#SelectIconModal", { backdrop: true, keyboard: true }); }}>
                            <i className="far fa-icons icon"></i>
                            <span>Ícone</span>
                        </DropdownItem>


                        <DropdownItem disabled={loading || isSubCategory} onClick={() => {

                            OpenModal("#SelectColorModal", { backdrop: true, keyboard: true });

                        }}>
                            <i className="far fa-palette icon"></i>
                            <div>
                                <span>Cor de Destaque</span>
                                {
                                    isSubCategory  &&
                                    <small className="text-muted d-block text-sm" style={{ fontSize: "11px", whiteSpace: "pre-wrap" }}>
                                        Altere a cor na categoria pai.
                                    </small>
                                }
                            </div>


                        </DropdownItem>



                        <DropdownDivider />

                        <DropdownItem disabled={loading || amountExpenses == 0}
                            onClick={() => { OpenModal("#SelectCategoryModal", { backdrop: true, keyboard: true }) }}>
                            <i className="far fa-exchange icon me-1"></i>
                            <div>
                                <span className="d-block">Transferir {type == 1 ? "Despesas" : "Transações"}</span>
                                {
                                    amountExpenses == 0 &&
                                    <small className="text-muted d-block text-sm" style={{ fontSize: "11px" }}>
                                        Nenhuma {type == 1 ? "despesa" : "transação"} vinculada.
                                    </small>
                                }
                            </div>
                        </DropdownItem>

                        <DropdownDivider />

                        <DropdownItem disabled={loading} onClick={() => { OpenModal("#DisabledCategoryConfirm"); }}>
                            <i class={`fas ${active ? "fa-toggle-off" : "fa-toggle-on"} icon me-1`}></i>


                            <div>
                                <span className="d-block">    {active ? "Desativar" : "Ativar"}</span>
                                {
                                    !active > 0 &&
                                    <small className="text-muted d-block text-sm" style={{ fontSize: "11px" }}>
                                        Ative a categoria para poder utilizá-la.
                                    </small>
                                }
                            </div>
                        </DropdownItem>

                        <DropdownDivider />

                        <DropdownItem disabled={loading || amountExpenses > 0} onClick={() => OpenModal("#DeleteCategoryConfirm")}>
                            <i className="far fa-trash-alt icon me-1"></i>
                            <div>
                                <span className="d-block">Excluir</span>
                                {
                                    amountExpenses > 0 &&
                                    <small className="text-muted d-block text-sm" style={{ fontSize: "11px" }}>
                                        Categoria tem {amountExpenses} {amountExpenses == 1 ? " despesa" : " despesas"} vinculadas.
                                    </small>
                                }
                            </div>
                        </DropdownItem>

                    </DropdownMenu>

                </Dropdown>

            </PageHeader>


            {/* <div className="appHeader text-white" style={{ backgroundColor: color }}>
                <div className="left">
                    <a href={undefined} className="headerButton text-white" onClick={() => window.history.back()}>
                        <i className="fal fa-chevron-left icon"></i>
                    </a>
                </div>
                <div className="pageTitle" onClick={() => {
                    const headerColor = GetHeaderColor();
                    alert(`Cor do Header: `, headerColor);
                }}>
                    Categoria
                </div>

                <div className="right">

                  
                </div>

            </div> */}

            <div className="section mt-2 mb-2">

                <div className="card mb-3 pb-2">
                    <div className="card-body pt-0">
                        <div className="listed-detail mt-3 text-center">
                            <div className="icon-wrapper" >
                                {
                                    loading ?
                                        <>
                                            <BoxShimmer style={{ width: "64px", height: "64px", borderRadius: "100%" }} />
                                        </>
                                        :
                                        <>
                                            <div className="iconbox" style={{ backgroundColor: color }}>
                                                <i id="icon" className={icon}></i>
                                            </div>
                                        </>
                                }
                            </div>
                            <h3 className="text-center mt-2">
                                {name || <LineShimmer style={{ width: "100px" }} />}
                            </h3>
                            <p className="text-center pb-0 py-0 mb-0 mt-0">
                                {description}
                            </p>
                            <span className="mt-0 pt-0">
                                {loading && <LineShimmer />}
                            </span>
                        </div>
                    </div>
                </div>

                <div className="card">
                    <div className="card-body pt-0">

                        <ul className="listview flush transparent simple-listview no-space mt-3">

                            <li>
                                <strong>
                                    <i className={`fas ${active ? "fa-toggle-on" : "fa-toggle-off"} icon me-1`}></i>
                                    Status
                                </strong>
                                <span className={active ? "text-success" : "text-secondary"}>
                                    {active ? "Ativa" : "Desativada"}
                                </span>
                            </li>

                            <li>
                                <strong>
                                    <i className="fas fa-ball-pile icon me-1"></i>
                                    Total de Despesas
                                </strong>
                                <span >{amountExpenses}</span>
                            </li>

                            <li>
                                <strong>
                                    <i className="fas fa-calendar icon me-1"></i>
                                    Criada em
                                </strong>
                                <span>{DateInWord(insertedDate, true)}</span>
                            </li>

                        </ul>

                    </div>
                </div>


                {
                    subCategories.length > 0 &&
                    <>
                        <div class="section-title mt-3">Sub-Categorias</div>
                        <PageCard padding="pt-1">

                            <ul className="listview image-listview border-0 flush">
                                {subCategories.map((item, i) => <Category key={i} {...item} />)}
                            </ul>

                        </PageCard>
                    </>
                }






                {/* Caixa de Erro */}
                {
                    error &&
                    <div className="card text-white bg-danger my-5">
                        <div className="card-body">
                            <h5 className="card-title">{error.title || "Erro Genérico"}</h5>
                            <ul className="card-text" >
                                {error.messages.map(message => <li>{message}</li>)}
                            </ul>
                        </div>
                    </div>
                }

            </div>

        </>
    )
}

