import { useState } from "react";
import PageHeader from "../../components/App/PageHeader";
import PageSection from "../../components/App/PageSection";
import { Post } from "../../utils/Request";
import MaskedInput from "react-text-mask";
import { createNumberMask } from "text-mask-addons";
import { SUCCESS_COLOR } from "../../utils/App";

export default function CreateWalletPage() {

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);


    const [name, setName] = useState("");
    const [type, setType] = useState("");
    const [balance, setBalance] = useState("");
    const [main, setMain] = useState(false);

    const [nameMessage, setNameMessage] = useState("");
    const [typeMessage, setTypeMessage] = useState("");
    const [balanceMessage, setBalanceMessage] = useState("");


    const currencyMask = createNumberMask({
        prefix: '',
        suffix: '',
        includeThousandsSeparator: true,
        thousandsSeparatorSymbol: '.',
        allowDecimal: true,
        decimalSymbol: ',',
        decimalLimit: 2, // how many digits allowed after the decimal
        integerLimit: 4, // limit length of integer numbers
        allowNegative: false,
        requireDecimal: false,
        allowLeadingZeroes: false,
    });

    async function CreateWallet() {

        const validate = ValidateInputs();
        if (validate == false) { return; }

        setLoading(true);
        setError(null);

        try {
            await Post("/wallets", { name, type, balance, main });
            window.location.href = "#/carteiras";
        }
        catch (error) {
            console.error("FALHA AO CRIAR CARTEIRA", error);
            setLoading(false);
            setError(error);
        }

    }

    function ValidateInputs() {

        if (!name) {
            setNameMessage("Digite um nome para a carteira.");
            document.getElementById("nameInput").focus();
            return false;
        }

        if (!type) {
            setTypeMessage("Selecione um tipo de carteira.");
            document.getElementById("typeInput").focus();
            return false;
        }

        if (!balance) {
            setBalanceMessage("Informe o saldo inicial da carteira");
            document.getElementById("balanceInput").focus();
            return false;
        }

        return true;

    }

    return (
        <>

            <PageHeader
                title={"Criar Carteira"}
                color={SUCCESS_COLOR}
                showBackButton={true}
                showActionButton={false}
            />

            <PageSection>

                <div className="card">
                    <div className="card-body">

                        {/* Nome */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (name ? "not-empty" : "")}>
                                <label className="label" htmlFor="nameInput">
                                    <i className="fas fa-quote-right icon me-1"></i>
                                    Nome
                                </label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="nameInput"
                                    maxLength={50}
                                    placeholder="Ex. Banco 01"
                                    disabled={loading}
                                    value={name}
                                    onKeyDown={() => setNameMessage("")}
                                    onChange={({ target }) => setName(target.value)}
                                />
                                <i className="clear-input" onClick={() => setName("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className={`input-info ${nameMessage && "text-danger"}`}>
                                {nameMessage}
                            </div>
                        </div>

                        {/* Tipo */}
                        <div className="form-group basic">
                            <div className={"input-wrapper"}>
                                <label className="label" htmlFor="typeInput">
                                    <i class="fas fa-dot-circle icon me-1"></i>
                                    Tipo
                                </label>
                                
                                <select
                                    className="form-control custom-select"
                                    id="typeInput"
                                    disabled={loading}
                                    onChange={({ target }) => {
                                        setType(target.value);
                                        setTypeMessage("");
                                    }}
                                >
                                    <option value={-1} selected disabled>Selecionar...</option>
                                    <option value={1} >Conta Corrente</option>
                                    <option value={2}>Poupança</option>
                                    <option value={3}>Investimento</option>
                                    <option value={4}>Outros</option>
                                </select>
                                <i className="clear-input" onClick={() => setType("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className={`input-info ${typeMessage && "text-danger"}`}>
                                {typeMessage}
                            </div>
                        </div>


                        {/* Saldo Inicial */}
                        <div className="form-group basic">
                            <div className={"input-wrapper " + (balance ? "not-empty" : "")}>
                                <label className="label" htmlFor="balanceInput">
                                    <i className="fas fa-usd-circle icon me-1"></i>
                                    Saldo Inicial
                                </label>
                                <div className="input-group align-items-center">
                                    <span className="input-group-text pe-1" >R$</span>
                                    <MaskedInput
                                        className="form-control"
                                        id="balanceInput"
                                        autoComplete="off"
                                        placeholder="0,00"

                                        mask={currencyMask}
                                        maxLength={12}
                                        disabled={loading}
                                        value={balance}
                                        onKeyDown={() => setBalanceMessage("")}
                                        onChange={({ target }) => setBalance(target.value)}
                                    />
                                </div>
                                <i className="clear-input" onClick={() => setBalance("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className={`input-info ${balanceMessage && "text-danger"}`}>
                                {balanceMessage}
                            </div>
                        </div>

                        {/* Carteira Principal */}
                        <ul class="listview simple-listview transparent flush">
                            <li>
                                <div>Carteira Principal</div>
                                <div class="form-check form-switch">
                                    <input
                                        class="form-check-input"
                                        type="checkbox"
                                        id="mainWalletCheck"
                                        checked={main}
                                        onChange={({ target }) => setMain(target.checked)}
                                    />
                                    <label class="form-check-label" htmlFor="mainWalletCheck"></label>
                                </div>
                            </li>
                        </ul>

                    </div>
                </div>


                <div className="form-button-group transparent">

                    <button
                        className="btn btn-block btn-lg btn-primary"
                        disabled={loading}
                        onClick={() => CreateWallet()}
                    >
                        {
                            loading ?
                                <>
                                    <i className="fas fa-spinner-third fa-spin icon me-2"></i>
                                    <span>Criando...</span>
                                </>
                                :
                                <>
                                    <span>Criar Carteira</span>
                                </>
                        }

                    </button>


                </div>

            </PageSection >

        </>
    )
}