import { useEffect, useState } from "react"
import { DecimalToMoney, GetDateString, Months } from "../utils/Functions";
import { Get } from "../utils/Request";
import Expense from "../components/Expenses/Expense";
import { SetThemeColor } from "../utils/App";

export default function DashboardPage() {


    const [status, setStatus] = useState(1);
    const [colorStatus, setColorStatus] = useState("");
    const [selectedDate, setSelectedDate] = useState(new Date());

    const [dashValues, setDashValues] = useState({});
    const [expenses, setExpenses] = useState([]);

    const statusText = ["", "Pagamentos em dia", "Pagamento atrasado!", "Aguardando pagamento...", "Tudo Pago"]
    const statusColor = ["", "#6236FF", "#FF396F", "#FFB400", "#1DCC70"];
    const statusIcon = ["", "far fa-check", "far fa-calendar-exclamation", "far fa-clock", "far fa-check-double"];

    /**
     * Avançar para o proximo mês.
     */
    const NextMonth = function () {
        const monthSelected = selectedDate.getMonth();
        setSelectedDate(new Date(selectedDate.setMonth(monthSelected + 1)));
    }

    /**
     * Retroceder para o mês anterior.
     */
    const BackMonth = function () {
        const monthSelected = selectedDate.getMonth();
        setSelectedDate(new Date(selectedDate.setMonth(monthSelected - 1)));
    }

    const GoToToday = async function () {
        await setSelectedDate(new Date());
    }

    async function GetValues() {
        try {
            const date = GetDateString(selectedDate);
            const result = await Get(`/dashboard/values?date=${date}`);
            setDashValues(result);
        }
        catch (error) {

        }
    }

    async function GetStatus() {
        try {
            const date = GetDateString(selectedDate);
            const result = await Get(`/dashboard/status?date=${date}`);
            setStatus(result.status);
            setColorStatus(statusColor[result.status])
            SetThemeColor(statusColor[result.status])
        }
        catch (error) {

        }
    }


    async function GetExpenses() {
        try {
            const date = GetDateString(selectedDate);
            const result = await Get(`/expenses/from-date?date=${date}`);
            setExpenses(result);
        }
        catch (error) {

        }
    }

    useEffect(() => {
        GetValues();
        GetStatus();
        GetExpenses();
    }, [selectedDate])

    useEffect(() => {
        SetThemeColor(colorStatus)
    }, [colorStatus])



    return (
        <>

            {/* Header */}
            <div className="appHeader text-white" style={{ background: colorStatus }}>

                <div className="left">
                    <a href={undefined} className="headerButton text-white" onClick={() => BackMonth()}>
                        <i className="fas fa-caret-left icon"></i>
                    </a>
                </div>

                <div className="pageTitle" onClick={() => GoToToday()}>
                    {
                        ((date) => {
                            const month = date.getMonth() + 1;
                            const year = date.getFullYear();
                            return (Months[month] + " de " + year);
                        })(selectedDate)
                    }
                </div>

                <div className="right">
                    <a href={undefined} className="headerButton text-white" onClick={() => NextMonth()}>
                        <i className="fas fa-caret-right icon"></i>
                    </a>
                </div>

            </div>

            {/* Dash */}
            <div className="section full gradientSection py-3" style={{ background: colorStatus }} >
                <div className="in pt-2 pb-4">

                    <h4 className="title">Pendente</h4>
                    <h1 className="total" >
                        <span>R$ </span>
                        <span>{DecimalToMoney(dashValues.pending)}</span>
                    </h1>

                    <h4 className="caption" id="dashStatus">
                        <span className="iconbox text-white">
                            <i className={statusIcon[status]}></i>
                        </span>
                        <span>{statusText[status]}</span>
                    </h4>

                </div>
            </div>

            <div className="section mt-2 mb-2">

                {/* Valores */}
                <div className="row mt-2">
                    <div className="col-6">
                        <div className="stat-box">
                            <div className="title">
                                Pago
                            </div>
                            <div className="value text-success">
                                <span>R$ </span>
                                <span>{DecimalToMoney(dashValues.paid)}</span>
                            </div>
                        </div>
                    </div>
                    <div className="col-6">
                        <div className="stat-box">
                            <div className="title">
                                Despesas
                            </div>
                            <div className="value text-danger">
                                <span>R$ </span>
                                <span>{DecimalToMoney(dashValues.total)}</span>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Gráficos */}
                <div className="row mt-2">
                    <div className="col">
                        <div className="section-title">Despesas</div>
                        <div className="card">
                            <div className="card-body pe-0 ps-0">
                                <div id="chart-pie"></div>
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className="section-title">Categorias</div>
                        <div className="card">
                            <div className="card-body pe-0 ps-0">
                                <div id="chart-donut"></div>
                            </div>
                        </div>
                    </div>
                </div>


                <div className="transactions">

                    <div className="section-title pt-2 pb-2">
                        DESPESAS
                    </div>

                    {
                        expenses.length == 0 && "Nehuma despesa pendente."
                    }
                    {
                        expenses.map((expense) => <Expense {...expense} />)
                    }
                </div>

            </div>





        </>
    )

}