export default function SuccessDialog({ title, message }) {

    return (
        <>
            <div className="modal fade dialogbox" id="SuccessDialog" data-bs-backdrop="static" tabindex="-1" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-icon text-success">
                            <i className="fas fa-check-circle"></i>
                        </div>
                        <div className="modal-header">
                            <h5 className="modal-title">{title}</h5>
                        </div>
                        <div className="modal-body">
                            {message}
                        </div>
                        <div className="modal-footer">
                            <div className="btn-inline">
                                <a href={undefined} className="btn" data-bs-dismiss="modal">FECHAR</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}