import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Get } from "../../utils/Request";
import { DateInWord, DecimalToMoney } from "../../utils/Functions";

import Parcel from "../../components/Expenses/Parcel";
import { OpenModal } from "../../utils/Modals";
import ChangeCategoryDialog from "../../dialogs/changeCategory";
import { EXPENSE_STATUS } from "../../utils/Enums";
import { SetThemeColor } from "../../utils/App";

export default function ViewExpensePage({ refreshExpense, setRefreshExpense }) {

    const statusText = ["", "Pagamento Pendente", "Pagamento em Dia", "Pagamento Atrasado", "Despesa Finalizada"];
    const statusIcon = ["", "fas fa-clock", "fas fa-calendar-check", "fas fa-exclamation-triangle", "fas fa-check"];
    const statusColor = ["", "#958d9e", "#6236FF", "#FF396F", "#1DCC70"];

    const [expense, setExpense] = useState({});
    const [parcels, setParcels] = useState([]);

    const [loading, setLoading] = useState(false);


    const { title, description } = expense;                                 // Informações Básicas
    const { categoryID, creditCardID } = expense;                                   // IDs
    const { categoryName, icon, color } = expense;                                  // Categoria
    const { startDate, endDate, expirationDate, date, insertedDate } = expense;     // Datas
    const { value, valuePaid, valuePending } = expense;                             // Valores
    const { linkWithCreditCard, paymentInParcels, active } = expense;                       // Flags
    const { numberOfParcels, parcelsPaid, parcelsPending } = expense; // Numeração de Parcelas


    const {
        status,




        durationYears,
        durationMonths
    } = expense;

    const { id } = useParams();
    console.log("id", id);

    useEffect(() => {

        GetExpense();
        GetParcels();

    }, [])

    useEffect(() => {

        if (refreshExpense == true) {
            alert('veio true');
            GetExpense();
            GetParcels();
            setRefreshExpense(false);
        }

    }, [refreshExpense]);

    /**
     * Obter informações da despesa.
     */
    const GetExpense = async function () {
        try {
            const result = await Get(`/expenses/${id}`);
            SetThemeColor(result.color);
            setExpense(result);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR DESPESA", error);
        }
    }

    /**
     * Obter parcelas da despesa.
     */
    const GetParcels = async function () {
        try {
            const result = await Get(`/expenses/${id}/parcels`);
            console.log("PARCELAS", result);
            setParcels(result);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR PARCELAS DA DESPESA", error);
        }
    }

    return (
        <>

            <ChangeCategoryDialog title="Alterar Categoria" />

            <div className="appHeader text-white" style={{ backgroundColor: color }}>
                <div className="left">
                    <a href={undefined} className="headerButton text-white" onClick={() => window.history.back()}>
                        <i className="fal fa-chevron-left icon"></i>
                    </a>
                </div>
                <div className="pageTitle">
                    Despesa
                </div>
                <div className="right">

                    <div className="dropdown">
                        <a className="headerButton  text-white" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                            <i className="fal fa-ellipsis-v icon"></i>
                        </a>
                        <div className="dropdown-menu dropdown-menu-end" style={{ width: "300px" }}>

                            <h1 className="dropdown-header">Editar</h1>


                            <a className="dropdown-item" href={undefined} >
                                <i className="far fa-quote-right icon"></i>
                                <span>Título</span>
                            </a>

                            <a className="dropdown-item" href={undefined} >
                                <i className="far fa-sticky-note icon"></i>
                                <span>Descrição</span>
                            </a>


                            <a
                                href={undefined}
                                className="dropdown-item"
                                onClick={() => {
                                    OpenModal("#ChangeCategoryDialog");
                                }}
                            >
                                <i className="far fa-tag icon"></i>
                                <span>Categoria</span>
                            </a>


                            <a className="dropdown-item" href={undefined} >
                                <i className="far fa-calendar icon"></i>
                                <span>Data</span>
                            </a>

                            {
                                !paymentInParcels &&
                                <>
                                    <div className="dropdown-divider"></div>
                                    <a className="dropdown-item" href={undefined} >
                                        <i className="far fa-hand-holding-usd icon"></i>
                                        <span>Pagar</span>
                                    </a>

                                </>
                            }


                            {
                                numberOfParcels == 1 &&
                                <>
                                    <a
                                        href={undefined}
                                        className={"dropdown-item " + (loading && "disabled")}
                                        onClick={() => {

                                        }}
                                    >
                                        <i className="far fa-calendar-day icon"></i>
                                        <span>Alterar Vencimento</span>
                                    </a>

                                    <a
                                        href={undefined}
                                        className={"dropdown-item " + (loading && "disabled")}
                                        onClick={() => {

                                        }}
                                    >
                                        <i className="far fa-usd-circle icon"></i>
                                        <span>Alterar Valor</span>
                                    </a>

                                    <a
                                        href={undefined}
                                        className={"dropdown-item " + (loading && "disabled")}
                                        onClick={() => {

                                        }}
                                    >
                                        <i className="far fa-hand-holding-usd icon"></i>
                                        <span>Pagar</span>
                                    </a>


                                </>
                            }




                            {/* <a className="dropdown-item" href={undefined}>
                                <i className="far fa-check-double icon"></i>
                                <span>Finalizar</span>
                            </a> */}



                            {
                                paymentInParcels && <>

                                    <div className="dropdown-divider"></div>
                                    <h1 className="dropdown-header">Parcelas</h1>

                                    <a className="dropdown-item" href={undefined}>
                                        <i className="far fa-plus icon"></i>
                                        <span>Adicionar Parcela</span>
                                    </a>
                                </>
                            }




                            <div className="dropdown-divider"></div>

                            <a className="dropdown-item" href={undefined}>
                                <i className="far fa-trash-alt icon"></i>
                                <span>Excluir</span>
                            </a>

                        </div>
                    </div>
                </div>
            </div>

            <div className="section mt-2 mb-2">

                {/* Informações Gerais da Despesa */}
                <div className="card mb-3 pb-2">
                    <div className="card-body pt-0">
                        <div className="listed-detail mt-3 text-center">
                            <div className="icon-wrapper">
                                <div className="iconbox" style={{ backgroundColor: color }}>
                                    <i id="icon" className={icon}></i>
                                </div>
                            </div>
                            <h3 className="text-center mt-2">{title}</h3>
                            <p className="text-center">{description}</p>

                            {/* CHIP de Status da Despesa */}
                            <div className="chip chip-media m-1">
                                <i className="chip-icon" style={{ backgroundColor: statusColor[status] }}>
                                    <i className={statusIcon[status]}></i>
                                </i>
                                <span className="chip-label">{status && EXPENSE_STATUS[status].text}</span>
                            </div>

                            {/* CHIP de Categoria */}
                            <div className="chip chip-media m-1">
                                <i className="chip-icon" style={{ backgroundColor: color }}>
                                    <i className={icon}></i>
                                </i>
                                <span className="chip-label">{categoryName}</span>
                            </div>

                            {/* CHIP de Percelas */}
                            {
                                numberOfParcels > 1 &&
                                <div className="chip chip-media m-1">
                                    <i className="chip-icon bg-dark" >
                                        <i className="fas fa-ball-pile"></i>
                                    </i>
                                    <span className="chip-label">{numberOfParcels} Parcelas</span>
                                </div>
                            }
                        </div>
                    </div>
                </div>



                <div className="card">
                    <div className="card-body pt-0">

                        {
                            numberOfParcels > 1 &&
                            <ul className="nav nav-tabs lined" role="tablist">
                                <li className="nav-item">
                                    <a className="nav-link active" data-bs-toggle="tab" href="#informations-tab" role="tab"
                                        aria-selected="false">
                                        INFORMAÇÕES
                                    </a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" data-bs-toggle="tab" href="#parcels-tab" role="tab" aria-selected="true">
                                        PARCELAS
                                    </a>
                                </li>
                            </ul>
                        }

                        <div className="tab-content mt-2">


                            {/* INFORMAÇÕES */}
                            <div className="tab-pane fade active show" id="informations-tab" role="tabpanel">

                                <ul className="listview flush transparent simple-listview no-space mt-3">


                                    <div className="pb-1">
                                        <strong className="text-muted">VALORES</strong>
                                    </div>

                                    <li>
                                        <strong>
                                            <i className="fas fa-usd-circle icon me-1 "></i>
                                            Total
                                        </strong>
                                        <h3 className="text-danger">R$ {DecimalToMoney(value)}</h3>
                                    </li>

                                    <li>
                                        <strong>
                                            <i className="fas fa-sack-dollar icon me-1"></i>
                                            Pendente
                                        </strong>
                                        <span className="text-secondary">R$ {DecimalToMoney(valuePending)}</span>
                                    </li>

                                    <li>
                                        <strong>
                                            <i className="fas fa-hand-holding-usd icon me-1"></i>
                                            Pago
                                        </strong>
                                        <span className="text-success" >R$ {DecimalToMoney(valuePaid)}</span>
                                    </li>


                                    {
                                        numberOfParcels > 1 &&
                                        <>

                                            <div className="pt-3 pb-1">
                                                <strong className="text-muted">PARCELAS</strong>
                                            </div>

                                            <li>
                                                <strong>
                                                    <i className="fas fa-ball-pile icon me-1"></i>
                                                    Quantidade
                                                </strong>
                                                <span >
                                                    {numberOfParcels > 0 && numberOfParcels}
                                                    {numberOfParcels == 0 ? "Nenhuma" : numberOfParcels == 1 ? " parcela" : " parcelas"}
                                                </span>
                                            </li>

                                            <li>
                                                <strong>
                                                    <i className="fas fa-clock icon me-1"></i>
                                                    Pendentes
                                                </strong>
                                                <span >
                                                    {parcelsPending > 0 && parcelsPending}
                                                    {parcelsPending == 0 ? "Nenhuma" : parcelsPending == 1 ? " parcela" : " parcelas"}
                                                </span>
                                            </li>

                                            <li>
                                                <strong>
                                                    <i className="fas fa-check icon me-1"></i>
                                                    Concluídas
                                                </strong>
                                                <span >
                                                    {parcelsPaid > 0 && parcelsPaid}
                                                    {parcelsPaid == 0 ? "Nenhuma" : parcelsPaid == 1 ? " parcela" : " parcelas"}
                                                </span>
                                            </li>

                                        </>
                                    }



                                    <div className="pt-3 pb-1">
                                        <strong className="text-muted">DATAS</strong>
                                    </div>

                                    <li style={{ display: durationYears == 0 && durationMonths == 0 && "none" }}>
                                        <strong>
                                            <i className="fas fa-hourglass-half icon me-1"></i>
                                            Duração
                                        </strong>
                                        <span>
                                            {durationYears > 0 && (durationYears) + " anos "}
                                            {durationYears > 0 && durationMonths > 0 && " e "}
                                            {durationMonths > 0 && (durationMonths) + " meses"}
                                        </span>
                                    </li>

                                    {/* Primeiro Vencimento (Caso Parcelada) */}
                                    {
                                        paymentInParcels &&
                                        <li>
                                            <strong>
                                                <i className="fas fa-calendar-star icon me-1" />
                                                Primeiro Vencimento
                                            </strong>
                                            <span>{DateInWord(startDate)} </span>
                                        </li>
                                    }

                                    {/* Último Vencimento (Caso Parcelada) */}
                                    {
                                        paymentInParcels &&
                                        <li>
                                            <strong>
                                                <i className="fas fa-calendar-check icon me-1" />
                                                Último Vencimento
                                            </strong>
                                            <span>{DateInWord(endDate)} </span>
                                        </li>
                                    }

                                    {/* Data de Vencimento (Caso não Parcelada) */}
                                    {
                                        !paymentInParcels && expirationDate &&
                                        <li>
                                            <strong>
                                                <i className="fas fa-calendar-day icon me-1" />
                                                Vencimento
                                            </strong>
                                            <span>{DateInWord(expirationDate)} </span>
                                        </li>
                                    }

                                    {/* Data de Criação */}
                                    <li>
                                        <strong>
                                            <i className="fas fa-calendar-plus icon me-1"></i>
                                            Criada em
                                        </strong>
                                        <span>{DateInWord(insertedDate, true)}</span>
                                    </li>


                                </ul>


                            </div>



                            <div className="tab-pane fade" id="parcels-tab" role="tabpanel">



                                <ul className="listview image-listview border-0">

                                    {
                                        parcels.map((parcel, index) => <Parcel key={index}  {...parcel} />)
                                    }

                                </ul>


                            </div>

                        </div>
                    </div>
                </div>



            </div>



        </>
    )
}