
import './assets/css/style.css';

import AppRoutes from './AppRoutes';
import AppBottomMenu from './components/App/appBottomMenu';
import { useState } from 'react';
import { useEffect } from 'react';



const Authenticated = true;

export default function App() {

  const isDarkMode = localStorage["darkmode"] == "true";
  document.querySelector('body').setAttribute('class', isDarkMode ? "dark-mode" : "");

  const innerHeight = window.innerHeight;
  document.querySelector(":root").style.setProperty('--min-main-height', `${innerHeight}px`);


  const [actualPage, setActualPage] = useState("");
  const pagesWithAppBottomMenu = ["/", "/dashboard", "/carteiras", "/despesas", "/categorias", "/menu"];

  useEffect(() => {

    const hash = window.location.hash.replace("#", "");
    setActualPage(hash);

  }, []);

  window.onhashchange = function () {
    const hash = window.location.hash.replace("#", "");
    setActualPage(hash);
    console.log("hash", hash)
  }




  return (
    <>
   

      <main>
        <AppRoutes />
      </main>



      <AppBottomMenu />







    </>
  );
}

