
export const PRIMARY_COLOR = "#6236FF";
export const SECONDARY_COLOR = "#8494A8";
export const DANGER_COLOR = "#FF396F";
export const SUCCESS_COLOR = "#1DCC70";

export function SetThemeColor(color) {

    const appHeaderColor = document.querySelector(".appHeader").style.backgroundColor;
    document.querySelector('meta[name="theme-color"]').setAttribute("content", color || appHeaderColor)

}
