const { bootstrap } = window;

export function OpenModal(documentID, options = { backdrop: "static", focus: true, keyboard: false }) {

    const modalElement = document.querySelector(documentID);
    const modal = new bootstrap.Modal(modalElement, options);
    modal.show();

}

export function CloseModal(documentID) {

    const modalElement = document.querySelector(documentID);
    const modal = new bootstrap.Modal(modalElement);
    modal.hide();

}