import { BrowserRouter, HashRouter, Navigate, Route, Routes } from "react-router-dom";

import Dashboard from "./pages/DashboardPage";

// Despesas
import ManagerExpenses from "./pages/expenses/ManagerExpensesPage";
import ViewExpense from "./pages/expenses/ViewExpensePage";
import ViewParcel from "./pages/expenses/ViewParcelPage";
import CreateExpense from "./pages/expenses/CreateExpensePage";

// Carteiras
import ManagerWalletsPage from "./pages/wallets/ManagerWalletsPage";
import CreateWalletPage from "./pages/wallets/CreateWalletPage";

// Cartões de Crédito
import ManagerCreditCardsPage from "./pages/credit-cards/ManagerCreditCardsPage";
import CreateCreditCardPage from "./pages/credit-cards/CreateCreditCardPage";

// Categorias
import ManagerCategories from "./pages/categories/ManagerCategoriesPage";
import ViewCategory from "./pages/categories/ViewCategoryPage";
import CreateCategory from "./pages/categories/CreateCategoryPage";

// Membros
import ManagerMembersPage from "./pages/members/ManagerMembersPage";

import Menu from "./pages/menu/MenuPage";


import Login from "./pages/external/LoginPage";
import Register from "./pages/external/RegisterPage";
import { useState } from "react";








export default function AppRoutes() {


  const [refreshExpense, setRefreshExpense] = useState(false);

  return (

    <HashRouter>


      <Routes>



        {/* Dashboard */}
        <Route exact path="/" element={<Navigate to="/dashboard" />} />
        <Route exact path="/dashboard" element={<Dashboard />} />

        {/* Despesas */}
        <Route exact path="/despesas" element={<ManagerExpenses />} />

        <Route exact path="/despesa/:id" element={
          <ViewExpense
            refreshExpense={refreshExpense}
            setRefreshExpense={setRefreshExpense}
          />
        } />

        <Route exact path="/parcela/:id" element={<ViewParcel setRefreshExpense={setRefreshExpense} />} />
        <Route exact path="/criar-despesa" element={<CreateExpense />} />

        {/* Cartões de Crédito */}
        <Route exact path="/cartoes-de-credito" element={<ManagerCreditCardsPage />} />
        <Route exact path="/criar-cartao-de-credito" element={<CreateCreditCardPage />} />


        {/* Carteiras */}
        <Route exact path="/carteiras" element={<ManagerWalletsPage />} />
        <Route exact path="/criar-carteira" element={<CreateWalletPage />} />

        {/* Membros */}
        <Route exact path="/membros" element={<ManagerMembersPage />} />


        {/* Categorias */}
        <Route exact path="/categorias" element={<ManagerCategories />} />
        <Route exact path="/categoria/:id" element={<ViewCategory />} />
        <Route exact path="/criar-categoria" element={<CreateCategory />} />

        {/* Menu */}
        <Route exact path="/menu" element={<Menu />} />

        {/* Boas Vindas / Login / Cadastro */}
        <Route exact path="/splashscreen" element={<></>} />
        <Route exact path="/login" element={<Login />} />
        <Route exact path="/criar-conta" element={<Register />} />



        {/* Erro 404 */}
        <Route exact path="/404" element={<p className="m-5">Página não encontrada - {window.location.href}</p>} />
        <Route exact path="*" element={<Navigate to="/404" />} />

      </Routes>


    </HashRouter>

  );
}
