import { useEffect, useState } from 'react';
import Category from '../../components/Categories/Category';
import { Get } from '../../utils/Request';
import { GetHeaderColor, PRIMARY_COLOR } from '../../utils/App';
import PageCard from '../../components/App/PageCard';
import PageHeader from '../../components/App/PageHeader';
import PageSection from '../../components/App/PageSection';
import Card, { CardBody } from '../../components/Template/Card';


export default function Categories() {

    const [categories, setCategories] = useState([]);
    const [showFilter, setShowFilter] = useState(false);

    useEffect(() => {

        const newCategoryID = sessionStorage["newCategoryID"];
        if (newCategoryID) {
            window.location.href = `#/categoria/${newCategoryID}`
            sessionStorage.removeItem("newCategoryID");
            return;
        }

        GetCategories();
    }, [])

    const GetCategories = async function () {
        try {
            const result = await Get("/categories/all?withSubs=true");
            setCategories(Array.isArray(result) ? result : []);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR CATEGORIAS", error);
        }
    }

    return (
        <>

            <PageHeader
                title="Categorias"
                color={PRIMARY_COLOR}
                showBackButton={true}
                showActionButton={true}
                actionButtonIcon="fal fa-plus icon"
                actionButtonCallback={() => {
                    window.location.href = "#/criar-categoria";
                }}
            />


            <PageSection>


                <Card>
                    <CardBody>

                        <ul className="nav nav-tabs lined" role="tablist">
                            <li className="nav-item">
                                <a className="nav-link active" data-bs-toggle="tab" href="#expenses-tab" role="tab"
                                    aria-selected="false">
                                    DESPESAS
                                </a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" data-bs-toggle="tab" href="#revenues-tab" role="tab" aria-selected="true">
                                    RECEITAS
                                </a>
                            </li>
                        </ul>

                        <div className="tab-content mt-2">

                            {/* GUIA - DESPESAS */}
                            <div className="tab-pane fade active show" id="expenses-tab" role="tabpanel">
                                <ul className="listview image-listview border-0">
                                    {
                                        categories.length == 0
                                            ?
                                            <>
                                                <p>Nenhuma categoria encontrada...</p>
                                            </>
                                            :
                                            <>
                                                {
                                                    categories.filter(x => x.active && x.type == 1).map((category, i) => {

                                                        const { subCategories } = category;

                                                        return (
                                                            <>
                                                                <Category  {...category} />
                                                                {subCategories.map((subCategory, i) => {
                                                                    return (
                                                                        <Category {...subCategory} sub={true} />
                                                                    )
                                                                })}
                                                            </>
                                                        )


                                                    })
                                                }
                                                {categories.filter(x => x.active == false && x.type == 1).map((category) => <Category  {...category} />)}
                                            </>
                                    }
                                </ul>
                            </div>

                            {/* GUIA - RECEITAS */}
                            <div className="tab-pane fade" id="revenues-tab" role="tabpanel">
                                <ul className="listview image-listview border-0">
                                    {
                                        categories.length == 0
                                            ?
                                            <>
                                                <p>Nenhuma categoria encontrada...</p>
                                            </>
                                            :
                                            <>
                                                {
                                                    categories.filter(x => x.active && x.type == 2).map((category, i) => {

                                                        const { subCategories } = category;

                                                        return (
                                                            <>
                                                                <Category  {...category} />
                                                                {subCategories.map((subCategory, i) => {
                                                                    return (
                                                                        <Category {...subCategory} sub={true} />
                                                                    )
                                                                })}
                                                            </>
                                                        )


                                                    })
                                                }
                                                {categories.filter(x => x.active == false && x.type == 2).map((category) => <Category  {...category} />)}
                                            </>
                                    }
                                </ul>
                            </div>


                        </div>
                    </CardBody>
                </Card>




            </PageSection>



        </>
    );
}