export function Dropdown({ children }) {
    return (
        <>
            <div className="dropdown">
                {children}
            </div>
        </>
    )
}

export function DropdownMenu({ children, style }) {
    return (
        <div className="dropdown-menu dropdown-menu-end" style={style}>
            {children}
        </div>
    )
}

export function DropdownHeader({ text }) {
    return (
        <h1 className="dropdown-header">{text}</h1>
    );
}

export function DropdownItem(props) {

    const {
        children,
        disabled = false,
        onClick = () => { }
    } = props;

    return (
        <button
            class={`dropdown-item ${disabled && "disabled"}`}
            type="button"
            disabled={disabled}
            onClick={(e) => onClick(e)}
        >
            {children}
        </button>
    );
}

export function DropdownDivider() {
    return (
        <div className="dropdown-divider" />
    );
}