import { useState } from "react";
import ColorItem from "../../components/Categories/ColorItem";
import IconItem from "../../components/Categories/IconItem";
import { Get, Post } from "../../utils/Request";
import PageHeader from "../../components/App/PageHeader";
import PageSection from "../../components/App/PageSection";
import PageCard from "../../components/App/PageCard";
import SelectColorModal, { Color } from "../../modals/SelectColor";
import { OpenModal } from "../../utils/Modals";
import SelectIconModal, { Icon } from "../../modals/SelectIcon";
import { useEffect } from "react";
import ErrorBox from "../../components/Template/ErrorBox";
import { DANGER_COLOR, SUCCESS_COLOR } from "../../utils/App";

export default function CreateCategoryPage() {

    const COLORS = ["#4496C8", "#8D3BC4", "#71962C", "#BB271B"];
    const ICONS = ["fas fa-popcorn", "fas fa-building", "fas fa-user-tie", 'fas fa-mobile'];
    const TYPE_TEXT = ["", "Despesa", "Receita"];

    const [parentCategories, setParentCategories] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [type, setType] = useState(1);
    const [parentCategory, setParentCategory] = useState(null);
    const [color, setColor] = useState();
    const [icon, setIcon] = useState();
    const [active, setActive] = useState(true);

    const [nameMessage, setNameMessage] = useState("");
    const [descriptionMessage, setDescriptionMessage] = useState("");
    const [typeMessage, setTypeMessage] = useState("");
    const [colorMessage, setColorMessage] = useState("");
    const [iconMessage, setIconMessage] = useState("");

    useEffect(() => {
        GetParentCategories();
    }, [])


    async function GetParentCategories() {
        try {
            const result = await Get("/categories/all");
            setParentCategories(Array.isArray(result) ? result : []);
        } catch (error) {

        }
    }

    /**
     * Criar Categoria.
     */
    async function CreateCategory() {

        const validate = Validate();
        if (!validate) { return }

        setLoading(true);
        setError(null);

        const parentCategoryID = parentCategory && parentCategory.id;

        try {
            const result = await Post("/categories", { name, description, color, icon, type, active, parentCategoryID });
            sessionStorage["newCategoryID"] = result.id;
            window.location.href = "#/categorias";
        }
        catch (error) {
            console.error("FALHA AO CRIAR CATEGORIA", error);
            setLoading(false);
            setError(error);
        }
    }

    function Validate() {

        if (!name) {
            document.getElementById("nameInput").focus();
            setNameMessage("Digite o nome da nova categoria.");
            return false;
        }

        if (!type) {
            document.getElementById("selectCategoryTypeButton").focus();
            setTypeMessage("Selecione o tipo de categoria.");
            return false;
        }

        if (!color) {
            setColorMessage("Selecione uma cor para a nova categoria.");
            return false;
        }

        if (!icon) {
            setIconMessage("Selecione um ícone para a nova categoria.");
            return false;
        }

        return true;

    }

    return (
        <>

            <SelectColorModal
                oldColor={color}
                confirmCallBack={({ value }) => setColor(value)}
            />

            <SelectIconModal
                oldIcon={icon}
                confirmCallBack={({ value }) => setIcon(value)}
            />

            <PageHeader
                title={"Criar Categoria"}
                showBackButton={true}
                showActionButton={false}
            />

            <PageSection>

                <PageCard>

                    {/* Nome */}
                    <div className="form-group basic mt-3">
                        <div className={`input-wrapper ${name && "not-empty"}`}>
                            <label className="label" htmlFor="nameInput">
                                <i className="fas fa-quote-right icon me-1"></i>
                                Nome
                            </label>
                            <input
                                type="text"
                                className="form-control"
                                id="nameInput"
                                maxLength={50}
                                placeholder="Ex. Categoria"
                                disabled={loading}
                                value={name}
                                onKeyDown={() => setNameMessage("")}
                                onChange={({ target }) => setName(target.value)}
                            />
                            <i className="clear-input" onClick={() => setName("")}>
                                <i className="fal fa-times icon"></i>
                            </i>
                        </div>
                        <div className="input-info">
                            <span className="text-danger">
                                {nameMessage}
                            </span>
                            <span className="float-end">
                                {50 - name.length}
                            </span>
                        </div>
                    </div>

                    {/* Descrição */}
                    <div className="form-group basic mt-1">
                        <div className={`input-wrapper ${description && "not-empty"}`}>
                            <label className="label" htmlFor="titleInput">
                                <i className="fas fa-sticky-note icon me-1"></i>
                                Descrição
                            </label>
                            <textarea
                                className="form-control"
                                id="titleInput"
                                maxLength={200}
                                placeholder="Ex. Descrição da categoria"
                                disabled={loading}
                                value={description}
                                onKeyDown={() => setDescriptionMessage("")}
                                onChange={({ target }) => setDescription(target.value)}
                                style={{ height: "80px" }}
                            />
                            <i className="clear-input" onClick={() => setDescription("")}>
                                <i className="fal fa-times icon"></i>
                            </i>
                        </div>
                        <div className="input-info">
                            <span className="text-danger">
                                {descriptionMessage}
                            </span>
                            <span className="float-end">
                                {200 - description.length}
                            </span>
                        </div>
                    </div>

                    {/* Tipo */}
                    <div className="form-group basic mt-3">
                        <div className={`input-wrapper`}>
                            <label className="label" htmlFor="typeSelect">
                                <i className="fas fa-hexagon icon me-1"></i>
                                Tipo
                            </label>
                            <div className="dropdown mt-1">
                                <button
                                    className="btn btn-block btn-outline-secondary dropdown-toggle justify-content-start"
                                    id="selectCategoryTypeButton"
                                    type="button"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false"
                                    style={{ backgroundColor: type == 1 ? DANGER_COLOR : SUCCESS_COLOR, color: "white" }}
                                >
                                    {
                                        type ?
                                            <>
                                                <i className={`fas ${type == 1 ? "fa-sack-dollar" : "fa-wallet"} me-1`} />
                                                <span>{TYPE_TEXT[type]}</span>
                                            </>
                                            :
                                            <>
                                                <i className="fas fa-hexagon me-1"></i>
                                                <span>Selecionar Tipo</span>
                                            </>

                                    }
                                </button>
                                <div className="dropdown-menu dropdown-menu-end" style={{ minWidth: "100%", maxHeight: "280px", overflowY: "auto" }}>
                                    <a
                                        href={undefined}
                                        className={`dropdown-item ${loading && "disabled"}`}
                                        onClick={() => {
                                            setType(1);
                                            setTypeMessage("");
                                            setParentCategory(null);
                                            document.getElementById("selectParentCategoryButton").classList.add("btn-outline-secondary");
                                            document.getElementById("selectCategoryTypeButton").classList.remove("btn-outline-secondary");
                                        }}
                                    >
                                        <i class="fas fa-sack-dollar icon me-1" style={{ color: DANGER_COLOR }} />
                                        Despesa
                                    </a>

                                    <a
                                        href={undefined}
                                        className={`dropdown-item ${loading && "disabled"}`}
                                        onClick={() => {
                                            setType(2);
                                            setTypeMessage("");
                                            setParentCategory(null);
                                            document.getElementById("selectParentCategoryButton").classList.add("btn-outline-secondary");
                                            document.getElementById("selectCategoryTypeButton").classList.remove("btn-outline-secondary");
                                        }}
                                    >
                                        <i class="fas fa-wallet icon me-1" style={{ color: SUCCESS_COLOR }} />
                                        Receita
                                    </a>
                                </div>
                            </div>
                        </div>
                        <div className="input-info">
                            <span className="text-danger">
                                {typeMessage}
                            </span>
                            <span className="float-end">
                            </span>
                        </div>
                    </div>

                    {/* Categoria Pai */}
                    <div className="form-group basic mt-3">
                        <div className={`input-wrapper`}>
                            <label className="label" htmlFor="typeSelect">
                                <i className="fas fa-link icon me-1"></i>
                                Categoria Pai
                            </label>
                            <div className="dropdown mt-1">
                                <button
                                    className="btn btn-block btn-outline-secondary dropdown-toggle justify-content-start"
                                    id="selectParentCategoryButton"
                                    type="button"
                                    data-bs-toggle="dropdown"
                                    aria-expanded="false"
                                    style={{ backgroundColor: parentCategory && parentCategory.color, color: parentCategory && "white" }}
                                >
                                    {
                                        parentCategory ?
                                            <>
                                                <i className={`${parentCategory.icon}  me-1`} />
                                                <span>{parentCategory.name}</span>
                                            </>
                                            :
                                            <>
                                                <i className="fas fa-tag me-1"></i>
                                                <span>Categoria Principal</span>
                                            </>

                                    }
                                </button>
                                <div className="dropdown-menu dropdown-menu-end" style={{ minWidth: "100%", maxHeight: "280px", overflowY: "auto" }}>
                                    {
                                        parentCategory &&
                                        <>
                                            <a
                                                href={undefined}
                                                className={`dropdown-item ${loading && "disabled"}`}
                                                onClick={() => {
                                                    document.getElementById("selectParentCategoryButton").classList.add("btn-outline-secondary");
                                                    setParentCategory(null);
                                                }}
                                            >
                                                <i class="fas fa-times-square icon me-1" />
                                                Não Definido
                                            </a>
                                            <div className="dropdown-divider" />
                                        </>

                                    }
                                    {
                                        parentCategories.filter(x => x.type == type).map((item, i) => {
                                            return (
                                                <>
                                                    <a
                                                        key={i}
                                                        href={undefined}
                                                        className={`dropdown-item ${loading && "disabled"}`}
                                                        onClick={() => {
                                                            document.getElementById("selectParentCategoryButton").classList.remove("btn-outline-secondary");
                                                            setParentCategory(item);
                                                            setColor(item.color);
                                                        }}
                                                    >
                                                        <i className={`${item.icon} icon me-1`} style={{ color: item.color }} />
                                                        {item.name}
                                                    </a>
                                                </>
                                            )
                                        })
                                    }
                                </div>
                            </div>


                        </div>
                    </div>

                    {/* Cor de Destaque */}
                    <div className="form-group basic mt-3" style={{ display: parentCategory && "none" }}>
                        <div className={`input-wrapper`}>
                            <label className="label" htmlFor="nameInput">
                                <i className="fas fa-palette icon me-1"></i>
                                Cor de Destaque
                            </label>

                            <form className="d-flex justify-content-start align-items-center flex-wrap mt-1">
                                {
                                    color && !COLORS.includes(color) && <Color color={color} selectedColor={color} setColor={setColor} />
                                }
                                {
                                    COLORS.map((item, i) =>
                                        <Color
                                            key={i}
                                            color={item}
                                            selectedColor={color}
                                            setColor={({ value }) => setColor(value)}
                                        />
                                    )
                                }

                                <button
                                    className="btn btn-outline-secondary btn-sm ms-1"
                                    type="button"
                                    style={{ borderRadius: "100%" }}
                                    onClick={() => {
                                        OpenModal("#SelectColorModal", { backdrop: true, keyboard: true });
                                    }}>
                                    <i class="far fa-ellipsis-h"></i>
                                </button>

                            </form>



                        </div>
                        <div className="input-info">
                            <span className="text-danger">
                                {colorMessage}
                            </span>
                            <span className="float-end">
                            </span>
                        </div>
                    </div>


                    {/* Ícone */}
                    <div className="form-group basic mt-3">
                        <div className={`input-wrapper`}>
                            <label className="label" htmlFor="nameInput">
                                <i className="fas fa-icons icon me-1"></i>
                                Ícone
                            </label>

                            <form className="d-flex justify-content-start align-items-center flex-wrap mt-1">
                                {
                                    icon && !ICONS.includes(icon) && <Icon icon={icon} selectedIcon={icon} setIcon={setIcon} modal={false} />
                                }
                                {
                                    ICONS.map((item, i) =>
                                        <Icon
                                            key={i}
                                            icon={item}
                                            selectedIcon={icon}
                                            setIcon={({ value }) => setIcon(value)}
                                            modal={false}
                                        />
                                    )
                                }

                                <button
                                    className="btn btn-outline-secondary ms-1 btn-sm"
                                    type="button"
                                    style={{ borderRadius: "100%" }}
                                    onClick={() => {
                                        OpenModal("#SelectIconModal", { backdrop: true, keyboard: true });
                                    }}>
                                    <i class="far fa-ellipsis-h"></i>
                                </button>

                            </form>



                        </div>
                        <div className="input-info">
                            <span className="text-danger">
                                {iconMessage}
                            </span>
                            <span className="float-end">
                            </span>
                        </div>
                    </div>

                    {/* Ativo */}
                    <ul class="listview simple-listview transparent flush mt-1 w-100">
                        <li className="w-100 px-1">
                            <div>

                                <span>{active ? "Ativa" : "Desativada"}</span>
                                <span className="d-block text-muted">
                                    {
                                        active
                                            ?
                                            "Essa categoria será criada e ficará disponível para uso."
                                            :
                                            "Esta categoria será criada, mas ficará indisponível para uso."
                                    }
                                </span>

                            </div>
                            <div class="form-check form-switch">
                                <input
                                    class="form-check-input"
                                    type="checkbox"
                                    id="activeCheck"
                                    checked={active}
                                    onChange={({ target }) => setActive(target.checked)}
                                />
                                <label class="form-check-label" htmlFor="activeCheck"></label>
                            </div>
                        </li>
                    </ul>

                    {/* // To do: IMPLEMENTAR CRIAÇÃO DE SUB-CATEGORIAS LOGO NA CRIAÇÃO DA CATEGORIA. */}

                </PageCard>


                {/* Caixa de Erro */}
                {
                    error && <ErrorBox {...error} />
                }



                <div className="form-button-group transparent">
                    <button
                        className="btn btn-block btn-lg btn-primary"
                        disabled={loading}
                        onClick={() => CreateCategory()}
                    >
                        {
                            loading ?
                                <>
                                    <i className="fas fa-spinner-third fa-spin icon me-2"></i>
                                    <span>Criando...</span>
                                </>
                                :
                                <>
                                    <span>Criar Categoria</span>
                                </>
                        }

                    </button>
                </div>

            </PageSection>



        </>
    );
}
