import { DecimalToMoney } from "../../utils/Functions";

export default function Expense(props) {

  const { id, title, description, color, icon, value, valuePending } = props;

  return (
    <>
      <a href={"#/despesa/" + id} expsne-id={id} className="item expense-component">

        <div className="col d-flex align-items-center">

          <div className="icon-box text-white" style={{ backgroundColor: color }}>
            <i className={icon}></i>
          </div>

          <div className="expense-infos">
            <strong name="title">
              {title}
            </strong>
            <div className="text-small text-secondary">
              {description}
            </div>
          </div>
        </div>

        <div className="col-3">
          <div className="text-end">
            <strong name="valuePending">
              R$ {DecimalToMoney(valuePending)}
            </strong>
            <div className="text-small" name="value">
              R$ {DecimalToMoney(value)}
            </div>
          </div>
        </div>
      </a>
    </>
  );
}
