import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Delete, Get, Patch } from "../../utils/Request";
import { DateInWord, DecimalToMoney } from "../../utils/Functions";
import ConfirmDialog from "../../dialogs/confirm";
import { OpenModal } from "../../utils/Modals";
import ChangeValueDialog from "../../dialogs/changeValue";
import ChangeDateDialog from "../../dialogs/changeDate";
import SuccessDialog from "../../dialogs/success";
import { SetThemeColor } from "../../utils/App";

export default function ViewParcelPage({ setRefreshExpense }) {

    const { id } = useParams();
    const [parcel, setParcel] = useState({});
    const [expense, setExpense] = useState({});
    const [color, setColor] = useState(null);
    const { expenseID, value, number, description, status, expirationDate, insertedDate } = parcel;
    const { title, numberOfParcels } = expense;

    const statusText = ['', "Pendente", "Aguardando Pagamento", "Atrasado", "Pago", "Parcialmente Pago"];
    const statusColor = ['', "text-secondary", "text-warning", "text-danger", "text-success", "text-success"];

    const [confirmDialogMessage, setConfirmDialogMessage] = useState("");


    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [success, setSuccess] = useState({});

    useEffect(() => {
        (async () => {
            await GetParcel();
            // await GetExpense();

        })()
    }, [])

    // useEffect(() => {
    //     (async () => { await GetExpense(); })()
    // }, [parcel]);

    /**
     * Obter informações da parcela.
     */
    const GetParcel = async function () {
        try {
            const result = await Get(`/expenses/parcels/${id}`);
            setParcel(result);
            await GetExpense(result.expenseID);
            setLoading(false);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR PARCELA", error);
            setError(error);
            setLoading(false);
        }
    }

    /**
     * Obter informações da despesa da parcela.
     */
    async function GetExpense(expenseID) {
        try {

            const result = await Get(`/expenses/${expenseID}`);
            setExpense(result);
            setColor(result.color);
            SetThemeColor(result.color);
        }
        catch (error) {
            console.error("FALHA AO BUSCAR DESPESA DA PARCELA", error);
            setError(error);
            setLoading(false);
        }
    }

    /**
     * Alterar valor da parcela.
     */
    async function ChangeValue({ value }) {
        console.log("VALOR RECEBIDO DA MODAL", value);

        setError(false);
        setLoading(true);

        try {
            await Patch(`/expenses/parcels/${id}/value`, { value });
            setLoading(false);
            setSuccess({ title: "Valor Alterado", message: "O valor da parcela foi alterado com êxito." });
            OpenModal("#SuccessDialog");
            GetParcel();
        }
        catch (error) {
            console.error("FALHA AO ALTERAR VALOR DA PARCELA", error);
            setError(error);
            setLoading(false);
        }
    }

    async function ChangeExpirationDate({ value }) {
        console.log("VALOR RECEBIDO DA MODAL", value);

        setError(false);
        setLoading(true);

        try {
            await Patch(`/expenses/parcels/${id}/expiration-date`, { expirationDate: value });
            setLoading(false);
            setSuccess({ title: "Vencimento Alterado", message: "A data de vencimento foi alterada com êxito." });
            OpenModal("#SuccessDialog");
            GetParcel();
        }
        catch (error) {
            console.error("FALHA AO ALTERAR VENCIMENTO DA PARCELA", error);
            setError(error);
            setLoading(false);
        }
    }

    async function ChangeStatus() {
        setError(false);
        setLoading(true);

        try {
            await Patch(`/expenses/parcels/${id}/status`, { status: status == 4 ? 1 : 4 });
            setLoading(false);
            setSuccess({ title: "Status Alterado", message: "O status foi alterado com êxito." });
            OpenModal("#SuccessDialog");
            GetParcel();
        }
        catch (error) {
            console.error("FALHA AO ALTERAR VENCIMENTO DA PARCELA", error);
            setError(error);
            setLoading(false);
        }
    }

    /**
     * Excluir parcela.
     */
    async function RemoveParcel() {

        setError(false);
        setLoading(true);

        try {
            await Delete(`/expenses/parcels/${id}`);
            setRefreshExpense(true);
            window.location.href = `#/despesa/${parcel.expenseID}`;
        }
        catch (error) {
            console.error("FALHA AO EXCLUIR PARCELA", error);
            setError(error);
            setLoading(false);
        }

    }

    return (
        <>

            <ConfirmDialog title="Excluir Parcela" message={confirmDialogMessage} confirmCallback={RemoveParcel} />

            <ChangeValueDialog title="Alterar Valor" oldValue={value} confirmCallback={ChangeValue} />

            <ChangeDateDialog title="Alterar Vencimento" oldValue={expirationDate} confirmCallback={ChangeExpirationDate} />

            <SuccessDialog {...success} />


            <div className="appHeader text-white" style={{ backgroundColor: color }}>
                <div className="left">
                    <a href={undefined} className="headerButton text-white" onClick={() => window.history.back()}>
                        <i className="fal fa-chevron-left icon"></i>
                    </a>
                </div>
                <div className="pageTitle">
                    Parcela
                </div>

                <div className="right">

                    <div className="dropdown">
                        <a className="headerButton  text-white" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                            <i className="fal fa-ellipsis-v icon"></i>
                        </a>
                        <div className="dropdown-menu dropdown-menu-end" style={{ width: "300px" }}>

                            <h1 className="dropdown-header">Parcela</h1>

                            <a
                                href={undefined}
                                className={"dropdown-item " + (loading && "disabled")}
                                onClick={() => {
                                    ChangeStatus();
                                }}
                            >
                                <i className="far fa-hand-holding-usd icon"></i>
                                <span style={{ fontWeight: "600" }}>{status == 4 ? "Remover Pagamento" : "Pagar"}</span>
                            </a>

                            <a
                                href={undefined}
                                className={"dropdown-item " + (loading && "disabled")}
                                onClick={() => {
                                    OpenModal("#ChangeValueDialog");
                                }}
                            >
                                <i className="far fa-usd-circle icon"></i>
                                <span>Editar Valor</span>
                            </a>

                            <a
                                href={undefined}
                                className={"dropdown-item " + (loading && "disabled")}
                                onClick={() => {
                                    OpenModal("#ChangeDateDialog");
                                }}
                            >
                                <i className="far fa-calendar-day icon"></i>
                                <span>Alterar Vencimento</span>
                            </a>

                            {/* <div className="dropdown-divider"></div> */}



                            <div className="dropdown-divider"></div>

                            <a
                                href={undefined}
                                className={"dropdown-item " + (loading && "disabled")}
                                onClick={() => {

                                    setConfirmDialogMessage("Tem certeza que deseja excluir essa parcela?");
                                    OpenModal("#ConfirmDialog");

                                }}
                            >
                                <i className="far fa-trash-alt icon"></i>
                                <span>Excluir</span>
                            </a>

                        </div>
                    </div>
                </div>

            </div>

            <div className="section mt-2 mb-2">

                <div className="card mb-2" style={{ display: !description ? "none" : "" }}>
                    <div className="card-body pb-0">
                        <p>{description}</p>
                    </div>
                </div>

                <div className="card py-0">
                    <div className="card-body pt-0">
                        <ul className="listview flush transparent simple-listview no-space mt-3">

                            <li>
                                <strong>
                                    <i className="fas fa-usd-circle icon me-1"></i>
                                    Valor
                                </strong>
                                <h3 className={statusColor[status] + " mb-0"}>R$ {DecimalToMoney(value)}</h3>
                            </li>

                            <li>
                                <strong>
                                    <i className="fas fa-hexagon icon me-1"></i>
                                    Status
                                </strong>
                                <h3 className={statusColor[status] + " mb-0"} >
                                    {statusText[status]}
                                </h3>
                            </li>




                            <li>
                                <strong>
                                    <i className="fas fa-ball-pile icon me-1"></i>
                                    Parcela
                                </strong>
                                <span>
                                    {number} de {numberOfParcels}
                                </span>
                            </li>


                            <li>
                                <strong>
                                    <i className="fas fa-sack-dollar icon me-1"></i>
                                    Despesa
                                </strong>
                                <span>
                                    {title}
                                </span>
                            </li>


                            <li>
                                <strong>
                                    <i className="fas fa-calendar-day  icon me-1"></i>
                                    Vencimento
                                </strong>
                                <span>{DateInWord(expirationDate)}</span>
                            </li>

                            <li>
                                <strong>
                                    <i className="fas fa-calendar icon me-1"></i>
                                    Criado em
                                </strong>
                                <span>{DateInWord(insertedDate, true)}</span>
                            </li>

                        </ul>

                    </div>
                </div>



                {/* Caixa de Erro */}
                {
                    error &&
                    <div className="card text-white bg-danger my-5">
                        <div className="card-body">
                            <h5 className="card-title">{error.title || "Erro Genérico"}</h5>
                            <ul className="card-text" >
                                {error.messages.map(message => <li>{message}</li>)}
                            </ul>
                        </div>
                    </div>
                }


            </div>

        </>
    );

}