import { useEffect, useState } from "react";
import { Get, Post } from "../../utils/Request";
import PageHeader from "../../components/App/PageHeader";
import PageSection from "../../components/App/PageSection";
import MaskedInput from 'react-text-mask'
import createNumberMask from 'text-mask-addons/dist/createNumberMask'
import { DecimalToMoney, ParseDate, ParseDecimal } from "../../utils/Functions";
import Card, { CardBody } from "../../components/Template/Card";
import PageCard from "../../components/App/PageCard";
import { DANGER_COLOR, SUCCESS_COLOR } from "../../utils/App";
import FormInput from "../../components/formInput";

export default function CreateExpensePage() {

    const today = new Date();
    const { day, month, year } = { day: today.getDate(), month: (today.getMonth() + 1) > 10 ? `0${today.getMonth() + 1}` : today.getMonth() + 1, year: today.getFullYear() }

    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const [categories, setCategories] = useState([]);
    const [creditCards, setCreditCards] = useState([]);

    // Informações
    const [value, setValue] = useState("");
    const [paid, setPaid] = useState(false);
    const [category, setCategory] = useState(null);
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [location, setLocation] = useState("");
    const [date, setDate] = useState(new Date().toISOString().slice(0, 10));
    const [expirationDate, setExpirationDate] = useState(null);

    // Opções
    const [linkWithCreditCard, setLinkWithCreditCard] = useState(false);
    const [paymentInParcels, setPaymentInParcels] = useState(false);
    const [ignore, setIgnore] = useState(false);

    // Opções - Cartão de Crédito:
    const [creditCard, setCreditCard] = useState(null);

    // Opções - Parcelamento:
    const [numberOfParcels, setNumberOfParcels] = useState(0);






    // [Messages] Informações 
    const [valueMessage, setValueMessage] = useState("");
    const [categoryMessage, setCategoryMessage] = useState("");
    const [titleMessage, setTitleMessage] = useState("");
    const [descriptionMessage, setDescriptionMessage] = useState("");
    const [startDateMessage, setStartDateMessage] = useState("");
    const [dateOfPurchaseMessage, setDateOfPurchaseMessage] = useState("");

    const [expirationDayMessage, setExpirationDayMessage] = useState("");

    const currencyMask = createNumberMask({
        prefix: '',
        suffix: '',
        includeThousandsSeparator: true,
        thousandsSeparatorSymbol: '.',
        allowDecimal: true,
        decimalSymbol: ',',
        decimalLimit: 2, // how many digits allowed after the decimal
        integerLimit: 7, // limit length of integer numbers
        allowNegative: false,
        requireDecimal: false,
        allowLeadingZeroes: false,
    });

    useEffect(() => {
        GetCategories();
        GetCreditCards();
    }, []);

    useEffect(() => {
        const expenseValue = parseFloat(value.replace(".", "").replace(",", "."));
        console.log("VALOR", expenseValue)
    }, [value])

    // useEffect(() => {
    //     if (paymentInParcels) {

    //         setStartDate(startDate ? startDate.slice(0, 7) : new Date().toISOString().slice(0, 7));

    //     }
    //     else {
    //         setStartDate(startDate ? `${startDate}-01` : null);
    //     }
    // }, [paymentInParcels])

    /**
     * Obter lista de categorias do usuário.
     */
    async function GetCategories() {
        try {
            const result = await Get("/categories/all?withSubs=true");
            setCategories(result);
        }
        catch (error) {
            console.error("FALHA AO OBTER CATEGORIAS DO USUÁRIO", error);
        }
    }

    /**
     * Obter lista de cartões de crédito do usuário.
     */
    async function GetCreditCards() {
        try {
            const result = await Get("/credit-cards");
            setCreditCards(result);
        }
        catch (error) {
            console.error("FALHA AO OBTER CARTÕES DE CRÉDITO DO USUÁRIO", error);
        }
    }

    /**
     * Criar Despesa;
     */
    async function CreateExpense() {

        const validate = Validate();
        if (validate == false) { return; }

        setLoading(true);
        setError(null);

        try {

            const result = await Post("/expenses", {
                categoryID: category ? category.id : null,
                creditCardID: linkWithCreditCard ? creditCard && creditCard.id : null,
                title,
                description,
                value: ParseDecimal(value),
                numberOfParcels: paymentInParcels ? numberOfParcels : null,
                expirationDate: paymentInParcels || !paid ? ParseDate(expirationDate) : null,
                linkWithCreditCard,
                paymentInParcels,
                paid,
                ignore,
                date: ParseDate(date)
            });

            window.location.href = `#/despesa/${result.id}`; // Abrir Despesa;
        }
        catch (error) {
            console.error("FALHA AO CRIAR DESPESA", error);
            setLoading(false);
            setError(error);
        }

    }

    /**
     * Validar Campos;
     */
    function Validate() {

        const onlyNumbers = new RegExp('^[0-9]+$')


        if (!value) {
            document.getElementById("valueInput").focus();
            setValueMessage("Informe o valor total da despesa.");
            return false;
        }

        if (value == 0) {
            document.getElementById("valueInput").focus();
            setValueMessage("O valor da despesa não pode ser igual à 0 (zero).");
            return false;
        }

        if (value < 0) {
            document.getElementById("valueInput").focus();
            setValueMessage("O valor da despesa não pode ser negativo.");
            return false;
        }

        if (!title) {
            document.getElementById("titleInput").focus();
            setTitleMessage("Escreve um nome para a sua despesa.");
            return false;
        }

        if (onlyNumbers.test(title)) {
            document.getElementById("titleInput").focus();
            setTitleMessage("O título da despesa não pode conter somente números.");
            return false;
        }



        return true;
    }

    return (
        <>

            <PageHeader
                title="Criar Despesa"
                color={DANGER_COLOR}
                showBackButton={true}
                showActionButton={false}
            />

            <PageSection>

                {/* INFORMAÇÕES */}
                <div className="card">
                    <div className="card-body">

                        {/* Valor */}
                        <div className="form-group basic">
                            <div className={"input-wrapper"}>
                                <label className="label" htmlFor="valueInput" style={{ fontWeight: 900 }}>
                                    <i className="fas fa-usd-circle icon me-1"></i>
                                    Valor
                                </label>
                                <div className="input-group input-group-big-custom align-items-center">
                                    <span
                                        className="input-group-text pe-1"
                                        style={{ color: paid ? SUCCESS_COLOR : DANGER_COLOR }}>
                                        R$
                                    </span>
                                    <MaskedInput
                                        className="form-control input-big-custom"
                                        id="valueInput"
                                        autoComplete="off"
                                        placeholder="0,00"

                                        mask={currencyMask}
                                        maxLength={12}
                                        disabled={loading}
                                        value={value}
                                        onKeyDown={() => setValueMessage("")}
                                        onChange={({ target }) => setValue(target.value)}
                                        style={{ color: paid ? SUCCESS_COLOR : DANGER_COLOR }}
                                    />
                                </div>

                                {
                                    value && !linkWithCreditCard &&
                                    <div class="form-check form-check-big" style={{ position: "absolute", right: 0, top: "18px" }}>

                                        <small className={`me-1 ${paid && "text-success"}`} >{paid ? "Pago!" : "Pago?"}</small>

                                        <input
                                            type="checkbox"
                                            class="form-check-input"
                                            id="paidCheckbox"
                                            disabled={loading || linkWithCreditCard}
                                            checked={paid}
                                            onChange={({ target }) => setPaid(target.checked)}
                                        />

                                        <label class="form-check-label" for="paidCheckbox"></label>
                                    </div>
                                }


                            </div>
                            <div className="input-info">
                                <span className="text-danger">
                                    {valueMessage}
                                </span>
                                <span className="float-end">
                                    {/* {paid && "Valor Pago"} */}
                                </span>
                            </div>
                        </div>


                        {/* Categoria */}
                        <div className="form-group basic">
                            <div className={"input-wrapper"}>
                                {/* <label className="label" htmlFor="categorySelect">* Categoria</label> */}
                                <div className="dropdown">
                                    <button
                                        className="btn btn-block btn-outline-secondary dropdown-toggle justify-content-start"
                                        id="selectCategoryButton"
                                        type="button"
                                        data-bs-toggle="dropdown"
                                        aria-expanded="false"
                                        style={{ backgroundColor: category && category.color, color: category && "white" }}
                                    >
                                        {
                                            category ?
                                                <>
                                                    <i className={`${category.icon}  me-1`} />
                                                    <span>{category.name}</span>
                                                </>
                                                :
                                                <>
                                                    <i className="fas fa-tag me-1"></i>
                                                    <span>Selecionar Categoria</span>
                                                </>

                                        }
                                    </button>
                                    <div className="dropdown-menu dropdown-menu-end" style={{ minWidth: "100%", maxHeight: "280px", overflowY: "auto" }}>
                                        {
                                            categories.filter(x => x.type == 1).map((item, x) => {

                                                const subs = item.subCategories || [];

                                                return (
                                                    <>

                                                        <a
                                                            key={x}
                                                            href={undefined}
                                                            className={`dropdown-item ${loading && "disabled"}`}
                                                            onClick={() => {
                                                                document.getElementById("selectCategoryButton").classList.remove("btn-outline-secondary")
                                                                setCategory(item);
                                                            }}
                                                        >
                                                            <i className={`${item.icon} icon me-1`} style={{ color: item.color }} />
                                                            {item.name}
                                                        </a>

                                                        {
                                                            subs.map((sub, y) => {
                                                                return (
                                                                    <a
                                                                        key={y}
                                                                        href={undefined}
                                                                        className={`dropdown-item ${loading && "disabled"}`}
                                                                        style={{ paddingLeft: "36px" }}
                                                                        onClick={() => {
                                                                            document.getElementById("selectCategoryButton").classList.remove("btn-outline-secondary")
                                                                            setCategory(sub);
                                                                        }}
                                                                    >
                                                                        <i className={`fas ${sub.icon} icon me-1`} style={{ color: sub.color, fontSize: "10px" }} />
                                                                        {sub.name}
                                                                    </a>
                                                                )
                                                            })
                                                        }

                                                    </>
                                                );

                                            }

                                            )
                                        }
                                    </div>
                                </div>
                                <i className="clear-input" onClick={() => setCategory(null)}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info">
                                <span className="text-danger">
                                    {categoryMessage}
                                </span>
                                <span className="float-end">
                                </span>
                            </div>
                        </div>


                        {/* Título */}
                        <FormInput
                            classComplement={"mt-3"}
                            type={"text"}
                            id={"titleInput"}
                            label={"Título"}
                            icon={"fas fa-quote-right"}
                            placeholder={"Ex. Despesa 01"}
                            maxLength={100}
                            value={title}
                            setValue={setTitle}
                            message={titleMessage}
                            setMessage={setTitleMessage}
                            displayLength={true}
                        />

                        {/* <div className="form-group basic mt-3">
                            <div className={"input-wrapper " + (title ? "not-empty" : "")}>
                                <label className="label" htmlFor="titleInput">
                                    <i className="fas fa-quote-right icon me-1"></i>
                                    Título
                                </label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="titleInput"
                                    maxLength={100}
                                    placeholder="Ex. Despesa 01"
                                    disabled={loading}
                                    value={title}
                                    onKeyDown={() => setTitleMessage("")}
                                    onChange={({ target }) => setTitle(target.value)}
                                />
                                <i className="clear-input" onClick={() => setTitle("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info">
                                <span className="text-danger">
                                    {titleMessage}
                                </span>
                                <span className="float-end">
                                    {100 - title.length}
                                </span>
                            </div>
                        </div> */}


                        {/* Descrição */}
                        <div className="form-group basic mt-1">
                            <div className={"input-wrapper " + (description ? "not-empty" : "")}>
                                <label className="label" htmlFor="titleInput">
                                    <i className="fas fa-sticky-note icon me-1"></i>
                                    Descrição
                                </label>
                                <textarea
                                    className="form-control"
                                    id="titleInput"
                                    maxLength={200}
                                    placeholder="Ex. Descrição da despesa"
                                    disabled={loading}
                                    value={description}
                                    onKeyDown={() => setDescriptionMessage("")}
                                    onChange={({ target }) => setDescription(target.value)}

                                />
                                <i className="clear-input" onClick={() => setDescription("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info">
                                <span className="text-danger">
                                    {descriptionMessage}
                                </span>
                                <span className="float-end">
                                    {200 - description.length}
                                </span>
                            </div>
                        </div>


                        {/* Local */}
                        <div className="form-group basic mt-1">
                            <div className={`input-wrapper ${location && "not-empty"}`}>
                                <label className="label" htmlFor="locationInput">
                                    <i className="fas fa-map-marker-alt icon me-1"></i>
                                    Local
                                </label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="locationInput"
                                    maxLength={100}
                                    placeholder="Ex. Loja de Roupas"
                                    disabled={loading}
                                    value={location}
                                    onKeyDown={() => { }}
                                    onChange={({ target }) => setLocation(target.value)}
                                />
                                <i className="clear-input" onClick={() => setLocation("")}>
                                    <i className="fal fa-times icon"></i>
                                </i>
                            </div>
                            <div className="input-info">
                                <span className="text-danger">
                                    {titleMessage}
                                </span>
                                <span className="float-end">
                                    {100 - location.length}
                                </span>
                            </div>
                        </div>


                        {/* Data */}
                        <div className="form-group basic mt-1">
                            <div className={`input-wrapper`}>
                                <label className="label" htmlFor="shoppingDateInput">
                                    <i className="fas fa-calendar-alt icon me-1"></i>
                                    Data
                                </label>
                                <input
                                    type="date"
                                    className="form-control"
                                    id="shoppingDateInput"
                                    disabled={loading}
                                    value={date}
                                    onKeyDown={() => setDateOfPurchaseMessage("")}
                                    onChange={({ target }) => setDate(target.value)}
                                />
                            </div>
                            <div className="input-info">
                                <span className="text-danger">
                                    {dateOfPurchaseMessage}
                                </span>
                                <span className="float-end">
                                </span>
                            </div>
                        </div>

                        {/* Vencimento */}
                        {
                            !paid &&
                            <>
                                <div className="form-group basic">
                                    <div className={`input-wrapper`}>
                                        <label className="label" htmlFor="firstExpirationDateInput">
                                            <i className="fas fa-calendar-day icon me-1"></i>
                                            {paymentInParcels ? "Primeiro Vencimento" : "Vencimento"}
                                        </label>
                                        <input
                                            type="date"
                                            className="form-control"
                                            id="firstExpirationDateInput"
                                            disabled={loading}
                                            value={expirationDate}
                                            // onKeyDown={() => setAmountParcels("")}
                                            onChange={({ target }) => setExpirationDate(target.value)}
                                        />
                                    </div>
                                    <div className="input-info">
                                        <span className="text-danger">
                                            {startDateMessage}
                                        </span>
                                        <span className="float-end">
                                            {
                                                expirationDate && paymentInParcels &&
                                                <>
                                                    O vencimento das parcelas será todo dia {expirationDate.split('-')[2]} de cada mês.
                                                </>
                                            }
                                        </span>
                                    </div>
                                </div>

                            </>
                        }




                    </div>
                </div>


                {/* 
                <div className="section-title mt-3">
                    <i className="fas fa-usd-square icon me-1"></i>
                    Parcelamento
                </div> */}

                {/* OPÇÕES */}
                <PageCard margin="mt-2 mb-5" padding="p-0" >


                    {/* Vincular com Cartão de Crédito */}
                    <ul class="listview simple-listview transparent flush mt-1 w-100">
                        <li className="w-100 px-1">
                            <div>
                                <span>Vincular com Cartão de Crédito</span>
                                <span className="d-block text-muted">
                                    Deseja vincular essa despesa junto com um cartão de crédito?
                                </span>
                            </div>
                            <div className="form-check form-switch">
                                <input
                                    className="form-check-input"
                                    type="checkbox"
                                    id="linkWithCreditCardCheck"
                                    disabled={loading}
                                    checked={linkWithCreditCard}
                                    onChange={({ target }) => {
                                        setLinkWithCreditCard(target.checked);
                                        if (target.checked) {
                                            setPaid(false);
                                            setIgnore(false);
                                        } else {
                                            setCreditCard(null);
                                        }
                                    }}
                                />
                                <label className="form-check-label" htmlFor="linkWithCreditCardCheck"></label>
                            </div>
                        </li>
                    </ul>

                    {/* Selecionar Cartão de Crédito */}
                    {
                        linkWithCreditCard &&
                        <>
                            <div className="form-group basic" >
                                <div className={"input-wrapper"}>
                                    <label className="label" htmlFor="titleInput">
                                        <i className="fas fa-credit-card icon me-1"></i>
                                        Cartão de Crédito
                                    </label>
                                    <div className="dropdown mt-1">
                                        <button
                                            className="btn btn-block btn-outline-secondary dropdown-toggle justify-content-start"
                                            id="selectCreditCardButton"
                                            type="button"
                                            data-bs-toggle="dropdown"
                                            aria-expanded="false"
                                            style={{ backgroundColor: creditCard && creditCard.color, color: creditCard && "white" }}
                                        >
                                            {
                                                creditCard ?
                                                    <>
                                                        <i className="fas fa-credit-card me-1 text-white" ></i>
                                                        <span>{creditCard.name}</span>
                                                    </>
                                                    :
                                                    <>
                                                        <i className="fas fa-credit-card me-1"></i>
                                                        <span>Selecionar Cartão de Crédito</span>
                                                    </>
                                            }
                                        </button>
                                        <div className="dropdown-menu dropdown-menu-end" style={{ minWidth: "100%", maxHeight: "280px", overflowY: "auto" }}>
                                            {
                                                creditCards.map((item, i) =>
                                                    <a
                                                        href={undefined}
                                                        className={`dropdown-item ${loading && "disabled"}`}
                                                        onClick={() => {
                                                            document.getElementById("selectCreditCardButton").classList.remove("btn-outline-secondary")
                                                            setCreditCard(item);
                                                        }}
                                                    >
                                                        <i className="fas fa-credit-card me-1" style={{ color: item.color }} />

                                                        <div>
                                                            <span className="d-block">{item.name}</span>
                                                            <small className="d-block text-muted">
                                                                LIMITE: R$ {DecimalToMoney(item.creditLimit)}
                                                            </small>
                                                        </div>
                                                    </a>
                                                )
                                            }
                                        </div>
                                    </div>
                                    <i className="clear-input" onClick={() => setCategory(null)}>
                                        <i className="fal fa-times icon"></i>
                                    </i>
                                </div>
                                <div className="input-info">
                                    <span className="text-danger">
                                        {categoryMessage}
                                    </span>
                                    <span className="float-end">
                                    </span>
                                </div>
                            </div>

                            <hr />

                        </>
                    }


                    {/* Despesa Parcelada */}
                    <ul class="listview simple-listview transparent flush mt-1 w-100">
                        <li className="w-100 px-1">
                            <div>
                                <span>Parcelada</span>
                                <span className="d-block text-muted">
                                    Deseja que o sistema considere o valor total parcelado?
                                </span>
                            </div>
                            <div className="form-check form-switch">
                                <input
                                    className="form-check-input"
                                    type="checkbox"
                                    id="paymentInParcelsCheck"
                                    disabled={loading}
                                    checked={paymentInParcels}
                                    onChange={({ target }) => setPaymentInParcels(target.checked)}
                                />
                                <label className="form-check-label" htmlFor="paymentInParcelsCheck"></label>
                            </div>
                        </li>
                    </ul>

                    {
                        paymentInParcels &&
                        <>

                            {/* Número de Parcelas */}
                            <div className="form-group boxed">
                                <div className={`input-wrapper`}>
                                    <label className="label" htmlFor="amountParcelsInput">
                                        <i className="fas fa-ball-pile icon me-1"></i>
                                        Número de Parcelas
                                    </label>
                                    <input
                                        type="number"
                                        className="form-control"
                                        id="amountParcelsInput"
                                        pattern="\d*"
                                        maxLength={3}
                                        placeholder="Ex. 3"
                                        disabled={loading}
                                        value={numberOfParcels}
                                        // onKeyDown={() => setAmountParcels("")}
                                        onChange={({ target }) => setNumberOfParcels(target.value)}
                                    />
                                    <i className="clear-input" onClick={() => setTitle("")}>
                                        <i className="fal fa-times icon"></i>
                                    </i>
                                </div>
                                <div className="input-info">
                                    <span className="text-danger">
                                        {titleMessage}
                                    </span>
                                    <span className="float-end">
                                        {numberOfParcels > 0 && value &&
                                            <>
                                                {numberOfParcels}x de   {
                                                    (() => {
                                                        const expenseValue = parseFloat(value.replace(".", "").replace(",", "."));
                                                        return DecimalToMoney(expenseValue / numberOfParcels)
                                                    })()

                                                }
                                            </>
                                        }
                                    </span>
                                </div>
                            </div>



                            <hr className="mt-3" />

                        </>
                    }

                    {/* Ignorar */}
                    <ul class="listview simple-listview transparent flush mt-1 w-100">
                        <li className="w-100 px-1">
                            <div style={{ opacity: (loading || linkWithCreditCard) && ".5" }}>
                                <span>Ignorar</span>
                                <span className="d-block text-muted">
                                    Indica se o sistema deve ignorar o valor dessa despesa nos relatórios.
                                </span>
                            </div>
                            <div className="form-check form-switch">
                                <input
                                    className="form-check-input"
                                    type="checkbox"
                                    id="ignoreCheck"
                                    disabled={loading || linkWithCreditCard}
                                    checked={ignore}
                                    onChange={({ target }) => setIgnore(target.checked)}
                                />
                                <label className="form-check-label" htmlFor="ignoreCheck"></label>
                            </div>
                        </li>
                    </ul>

                </PageCard>


                <div className="form-button-group transparent">
                    <button
                        className="btn btn-block btn-lg btn-primary"
                        disabled={loading}
                        onClick={() => CreateExpense()}
                    >
                        {
                            loading ?
                                <>
                                    <i className="fas fa-spinner-third fa-spin icon me-2"></i>
                                    <span>Criando...</span>
                                </>
                                :
                                <>
                                    <span>Criar Despesa</span>
                                </>
                        }

                    </button>
                </div>

            </PageSection >






        </>
    );
}