import { useEffect } from "react";
import { PRIMARY_COLOR } from "../../utils/App";

export default function PageHeader({
    title = "Página",
    color = PRIMARY_COLOR,
    showBackButton = true,
    showActionButton = true,
    actionButtonIcon = "fa-plus",
    actionButtonCallback = () => { },
    children
}) {

    return (
        <div className="appHeader no-border" style={{ backgroundColor: color }}>

            {
                showBackButton &&
                <div className="left">
                    <a href={undefined} className="headerButton text-white" onClick={() => window.history.back()}>
                        <i className="fal fa-chevron-left icon"></i>
                    </a>
                </div>
            }

            <div className="pageTitle text-white" >
                {title}
            </div>

            <div className="right">
                {
                    showActionButton ?
                        <a href={undefined} className="headerButton text-white" onClick={() => actionButtonCallback()}                        >
                            <i className={`fal ${actionButtonIcon} icon`}></i>
                        </a>
                        :
                        children
                }

            </div>



        </div>
    )

}


export function HeaderRight({ children }) {
    return (
        <div className="right">
            {children}
        </div>
    );
}