// Data:
export const Months = ['', 'Janeiro', 'Fevereiro', 'Março', 'Abril', 'Maio', 'Junho', 'Julho', 'Agosto', 'Setembro', 'Outubro', 'Novembro', 'Dezembro'];
export const DaysOfWeek = ['Segunda', 'Terça', 'Quarta', 'Quinta', 'Sexta', 'Sábado', 'Domingo'];

export const ParseDate = function (value) {
  const year = value.split("-")[0];
  const month = value.split("-")[1];
  const day = value.split("-")[2];
  return new Date(year, parseInt(month) - 1, day);
}

export const DateInWord = function (value, withTime = false) {
  const date = new Date(value);
  const d = date.getDate(), m = date.getMonth() + 1, y = date.getFullYear();
  const h = date.getHours(), mm = date.getMinutes();
  const time = (h >= 1 && h <= 9 ? '0' + h : h) + ':' + (mm >= 1 && mm <= 9 ? '0' + mm : mm)
  return (d >= 1 && d <= 9 ? '0' + d : d) + ' de ' + Months[m] + ' de ' + y + (withTime ? ' às ' + time : '');
}

export const DateToString = function (value, withTime = false) {
  const date = new Date(value);
  const d = date.getDate(), m = date.getMonth() + 1, y = date.getFullYear();
  const h = date.getHours(), mm = date.getMinutes();
  const time = (h >= 1 && h <= 9 ? '0' + h : h) + ':' + (mm >= 1 && mm <= 9 ? '0' + mm : mm)
  return (d >= 1 && d <= 9 ? '0' + d : d) + '/' + (m >= 1 && m <= 9 ? '0' + m : m) + '/' + y + (withTime ? ' ' + time : '');
}

export const GetDateString = function (date = new Date()) { return date.toJSON().toString(); }


// ---------------------------------------------- //
export const ParseDecimal = function (value = "") {
  return parseFloat(value.replace(".", "").replace(",", "."));
}

// Números
export const DecimalToMoney = function (number = 0) { return number.toLocaleString("pt-BR", { maximumFractionDigits: 2, minimumFractionDigits: 2 }); }

// Email:
export const IsEmail = function (value) { return /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(String(value).toLowerCase()); }

// Guid:
export const NewGuid = function () { return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) { var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8); return v.toString(16); }); }



// ---------------------------------------------- //
export function IsJSON(data) {
  try {
    JSON.parse(data);
    return true;
  } catch (error) {
    return false;
  }
}

